@use "../commons/var";
@use "../commons/mixins";

.header {
    padding: 12px 0 11px;
    border-bottom: 1px solid var.$border;
    z-index: 3;
    @include mixins.desktop {
        padding: 21px 0 20px;
    }
    &__inner {
        display: flex;
        align-items: center;
        @include mixins.tablet {
            justify-content: initial;
        }
    }
    &__logo {
        max-width: 116px;
        margin-right: auto;
    }
    &__menu {
        position: fixed;
        top: 64px;
        right: -102vw;
        opacity: 0;
        z-index: -1;
        width: 100%;
        height: calc(100% - 64px);
        transition: right 0.3s linear;
        text-align: center;
        display: flex;
        flex-direction: column;
        @include mixins.tablet {
            width: 280px;
            &::before {
                content: "";
                top: 64px;
                left: 0;
                width: 100vw;
                height: calc(100% - 64px);
                position: fixed;
                background-color: var.$bg-secondary;
                opacity: 0;
                transition: opacity 0.3s linear;
                z-index: var.$ui-index-4;
                pointer-events: none;
            }
        }
        @include mixins.desktop {
            position: initial;
            width: auto;
            height: auto;
            opacity: 1;
            flex-direction: row;
            z-index: 1;
            &::before {
                display: none;
            }
        }
        &.show {
            right: 0;
            opacity: 1;
            z-index: var.$ui-index-5;
            &::before {
                opacity: 0.8;
            }
        }
    }
    &__links {
        display: flex;
        flex-direction: column;
        gap: 32px;
        height: 100%;
        padding: 24px;
        background-color: var.$bg;
        z-index: var.$ui-index-5;
        position: relative;
        @include mixins.tablet {
            gap: 0;
        }
        @include mixins.desktop {
            flex-direction: row;
            align-items: center;
            gap: 24px;
            padding: 0;
        }
    }
    &__links-item {
        @include mixins.tablet {
            text-align: left;
            padding-block: 16px;
            border-bottom: 1px solid var.$border;
        }
        @include mixins.desktop {
            padding: 0;
            border-bottom: 0;
        }
        &:last-child {
            margin-top: auto;
            @include mixins.tablet {
                margin-top: 0;
                border-bottom: 0;
                flex-grow: 1;
                position: relative;
                display: flex;
                flex-direction: column;
                justify-content: flex-end;
                &::before {
                    content: "";
                    position: absolute;
                    top: -2px;
                    left: 0;
                    width: 100%;
                    height: 3px;
                    background-color: var.$bg;
                }
            }
            @include mixins.desktop {
                display: block;
                &::before {
                    display: none;
                }
            }
        }
        & a {
            @include mixins.desktop {
                font-size: 14px;
            }
            @include mixins.hover {
                color: var.$text-secondary;
            }
        }
    }
    &__user {
        display: none;
        @include mixins.tablet {
            display: block;
            margin-left: 16px;
        }
        &--mobile {
            display: block;
            margin: 0 auto;
            @include mixins.tablet {
                display: none;
            }
        }
        &-auth {
            position: relative;
            overflow: hidden;
            background-color: transparent;
            border-radius: 18px;
            border: 1px solid var.$border;
            padding: 8px 16px 8px 8px;
            font-size: 14px;
            font-weight: 600;
            color: var.$text-light;
            cursor: pointer;
            display: flex;
            align-items: center;
            gap: 8px;
            @include mixins.hover {
                background-color: var.$hover-additional;
                border-color: var.$hover-additional;
            }
            &::before {
                content: "";
                width: 20px;
                height: 20px;
                background: url(../img/icons/user.svg) center no-repeat;
            }
            & .tg-overlay {
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                opacity: 0;
                cursor: pointer;
            }
        }
        &-menu {
            position: relative;
            &.hide {
                display: none;
            }
            &.active .header__user-toggle {
                background-color: var.$hover-additional;
                border-color: var.$hover-additional;
                &::after {
                    transform: rotate(180deg);
                    background-color: var.$text-light;
                }
            }
        }
        &-toggle {
            min-width: 190px;
            background-color: transparent;
            border-radius: 18px;
            border: 1px solid var.$border;
            padding: 8px 10px 8px 8px;
            font-size: 14px;
            font-weight: 600;
            color: var.$text-light;
            cursor: pointer;
            display: flex;
            align-items: center;
            gap: 8px;
            & span {
                white-space: nowrap;
                max-width: 120px;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            @include mixins.hover {
                background-color: var.$hover-additional;
                border-color: var.$hover-additional;
            }
            &::before {
                content: "";
                width: 20px;
                height: 20px;
                background: url(../img/icons/user.svg) center no-repeat;
            }
            &::after {
                content: "";
                width: 16px;
                height: 16px;
                @include mixins.svg-mask("chevron");
                background-color: var.$text-secondary;
                margin-left: auto;
            }
        }
        &-dropdown {
            position: absolute;
            top: calc(100% + 2px);
            left: 0;
            width: 100%;
            border-radius: 12px;
            box-shadow: 0px 2px 8px 0px #00000040;
            background-color: var.$bg-secondary;
            padding: 6px;
            display: flex;
            flex-direction: column;
            gap: 6px;
            display: none;
            &.active {
                display: block;
            }
        }
        &-link {
            font-size: 14px;
            padding: 6px 8px;
            border-radius: 6px;
            color: var.$text-primary;
            display: flex;
            align-items: center;
            gap: 8px;
            &::before {
                content: "";
                width: 16px;
                height: 16px;
                @include mixins.svg-mask("ecosystem");
                background-color: var.$text-primary;
            }
            &--settings::before {
                mask-image: url("../img/icons/settings.svg");
            }
            @include mixins.hover {
                background-color: var.$border;
                color: var.$text-light;
                &::before {
                    background-color: var.$text-light;
                }
            }
        }
        &-logout {
            font-size: 14px;
            padding: 6px 8px;
            border-radius: 6px;
            color: var.$red;
            width: 100%;
            display: flex;
            align-items: center;
            gap: 8px;
            background-color: initial;
            border: 0;
            cursor: pointer;
            &::before {
                content: "";
                width: 16px;
                height: 16px;
                @include mixins.svg-mask("log-out");
                background-color: var.$red;
            }
            @include mixins.hover {
                background-color: var.$border;
                color: var.$text-light;
                &::before {
                    background-color: var.$text-light;
                }
            }
        }
    }
    &__tg {
        margin-top: auto;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        gap: 8px;
        padding: 8px 16px 8px 8px;
        border-radius: 24px;
        border: 1px solid var.$border;
        font-size: 14px;
        @include mixins.hover {
            background-color: var.$accent;
            & span {
                color: var.$text-dark;
            }
        }
        & img {
            width: 20px;
            height: 20px;
        }
    }
    &__burger {
        width: 40px;
        height: 40px;
        border-radius: 8px;
        background-color: var.$border;
        border: 0;
        margin-left: 16px;
        background-image: url(../img/icons/burger.svg);
        background-position: center;
        background-repeat: no-repeat;
        cursor: pointer;
        &.active {
            background-image: url(../img/icons/close.svg);
        }
        @include mixins.tablet {
            margin-left: 24px;
        }
        @include mixins.desktop {
            display: none;
        }
    }

    &__language {
        position: relative;
        @include mixins.tablet {
            margin-left: 24px;
        }
        &-button {
            min-width: 45px;
            font-size: 14px;
            padding: 0;
            border: 0;
            background-color: transparent;
            text-transform: capitalize;
            position: relative;
            @include mixins.flex-center-center;
            transition: color 0.1s linear;
            gap: 6px;
            cursor: pointer;
            @include mixins.hover {
                & .header__language-current {
                    color: var.$white;
                }
                &::before {
                    background-color: var.$white;
                }
                &::after {
                    background-color: var.$white;
                }
            }
            &.active {
                & .header__language-current {
                    color: var.$accent;
                }
                &::before {
                    background-color: var.$accent;
                }
                &::after {
                    transform: rotate(180deg);
                    background-color: var.$accent;
                }
            }
            &::after {
                content: "";
                width: 14px;
                height: 14px;
                mask-image: url(../img/icons/chevron.svg);
                mask-position: center;
                mask-size: contain;
                background-color: var.$text-primary;
                transition: all 0.1s linear;
                margin-left: -2px;
            }
            &::before {
                content: "";
                width: 16px;
                height: 16px;
                mask-image: url(../img/icons/planet.svg);
                mask-position: center;
                mask-size: contain;
                background-color: var.$text-primary;
                transition: all 0.1s linear;
            }
        }
        &-current {
            color: var.$text-primary;
            .active & {
                color: var.$accent;
            }
        }
        &-list {
            position: absolute;
            width: auto;
            top: calc(100% + 16px);
            left: calc(50% - 2px);
            transform: translateX(-50%);
            box-shadow: 0px 2px 8px 0px #00000040;
            background-color: var.$bg-secondary;
            padding: 6px;
            border-radius: 12px;
            &::after {
                content: "";
                position: absolute;
                top: -11px;
                left: calc(50% - 12px);
                width: 24px;
                height: 12px;
                background: url(../img/icons/beak.svg) center no-repeat;
            }
            &.hidden {
                display: none;
            }
        }
        &-item {
            & a {
                cursor: pointer;
                display: block;
                font-weight: 400;
                font-size: 14px;
                line-height: 1.3;
                cursor: pointer;
                color: var.$white;
                padding: 6px 8px;
                border-radius: 6px;
                text-align: center;
            }
            &:not(:first-child) {
                margin-top: 4px;
            }
            @include mixins.hover {
                & a {
                    background-color: var.$border;
                }
            }
        }
    }
}
