@use "../../commons/var" as v;
@use "../../commons/mixins" as m;

.address {
	position: relative;
	z-index: v.$ui-index-2;
	margin: 0 auto;
	@include m.tablet {
		grid-column: 1/3;
		width: 100%;
		max-width: 720px;
		margin: 0 auto;
	}
	@include m.desktop {
		grid-column: 2/3;
		grid-row: 1;
	}
	&__inner {
		position: relative;
		z-index: v.$ui-index-1;
		background-color: transparent;
		border-radius: 24px;
		padding: 32px;
		@include m.tablet {
			@include m.flex-center;
			gap: 32px;
		}
	}
	&__corners {
		content: '';
		position: absolute;
		z-index: v.$ui-index-2;
		width: 100%;
		height: 100%;
		top: 0;
		left: 0;
		pointer-events: none;
	}
	&__corner {
		position: absolute;
		width: 64px;
		height: 64px;
		background-repeat: no-repeat;
		background-position: center;
		&:nth-child(1) {
			top: 0;
			left: 0;
			background-image: url(../img/index/corner1.svg);
		}
		&:nth-child(2) {
			top: 0;
			right: 0;
			background-image: url(../img/index/corner2.svg);
		}
		&:nth-child(3) {
			bottom: 0;
			left: 0;
			background-image: url(../img/index/corner3.svg);
		}
		&:nth-child(4) {
			bottom: 0;
			right: 0;
			background-image: url(../img/index/corner4.svg);
		}
	}
	&__img {
		border-radius: 8px;
		overflow: hidden;
		width: 278px;
		aspect-ratio: 1/1;
		margin: 0 auto;
		& img {
			width: 100%;
			height: 100%;
		}
		@include m.tablet {
			width: 302px;
			margin: 0;
		}
		@include m.tablet {
			width: 218px;
			flex: 0 0 218px;
		}
	}
	&__label {
		margin-top: 32px;
		color: v.$text-primary;
		@include m.tablet {
			margin-top: 0;
		}
		@include m.desktop {
			font-size: 14px;
		}
	}
	&__title {
		margin-top: 8px;
		font-weight: 700;
		font-size: 24px;
		@include m.desktop {
			font-size: 20px;
		}
	}
	&__box {
		border: 1px solid v.$border;
		border-radius: 12px;
		padding: 15px;
		margin-top: 24px;
		@include m.desktop {
			margin-top: 22px;
		}
	}
	&__text {
		word-break: break-all;
		font-size: 14px;
		font-weight: 700;
	}
	&__btn {
		margin-top: 12px;
		font-weight: 500;
		font-size: 14px;
		line-height: 1.4;
		border-radius: 4px;
		border: 1px solid v.$border;
		background-color: transparent;
		padding: 3px 11px 3px 5px;
		@include m.flex-center;
		color: v.$text-light;
		gap: 6px;
		cursor: pointer;
		transition: all .2s ease-in;
		@include m.hover {
			border-color: v.$text-light;
		}
		@include m.desktop {
			font-size: 14px;
		}
		&::before {
			content: '';
			width: 16px;
			height: 16px;
			pointer-events: none;
			mask: url(../img/icons/copy.svg) center no-repeat;
			background-color: currentColor;
			mask-size: contain;
		}
		&.copied {
			background-color: #74EB6933;
			border-color: v.$accent;
			color: v.$accent;
			pointer-events: none;
			&::before {
				mask: initial;
				background: url(../img/icons/check-circle.svg) center no-repeat;
				background-size: contain;
			}
		}
	}
}