@use "../commons/var";
@use "../commons/mixins";

.contact-block {
    padding: 40px 0;
    background-color: var.$bg-secondary;
    text-align: center;
    overflow: hidden;
    @include mixins.tablet {
        padding: 64px 0;
        text-align: left;
        background-image: url(../img/article/article-footer.png);
        background-position: calc(100% - 24px) bottom;
        background-repeat: no-repeat;
        background-size: 282px 379px;
        position: relative;
        @include mixins.desktop {
            background-position-x: calc(
                (100vw - 1200px) / 2 + 1200px - 282px
            );
            background-position-y: bottom;
        }
    }
    @include mixins.desktop {
        &::before {
            width: 1200px;
            height: 924px;
            left: calc((100vw - 1200px) / 2 - 360px);
            bottom: -440px;
        }
    }
    &__inner {
        @include mixins.tablet {
            max-width: 414px;
            z-index: var.$ui-index-2;
            position: relative;
        }
        @include mixins.desktop {
            max-width: 640px;
        }
    }
    &__title {
        font-weight: 700;
        font-size: 24px;
        @include mixins.tablet {
            font-size: 32px;
        }
    }
    &__text {
        margin-top: 16px;
        font-size: 14px;
        @include mixins.tablet {
            margin-top: 24px;
            font-size: 16px;
        }
        @include mixins.desktop {
            font-size: 20px;
        }
    }
    &__links {
        margin-top: 24px;
        @include mixins.tablet {
            display: flex;
            gap: 24px;
        }
        @include mixins.desktop {
            margin-top: 32px;
        }
    }
    &__link {
        &:not(:first-child) {
            margin-top: 10px;
            @include mixins.tablet {
                margin-top: 0;
            }
        }
    }
    &__label {
        color: var.$text-secondary;
        font-size: 12px;
        @include mixins.tablet {
            font-size: 14px;
        }
    }
    &__tg {
        margin-top: 6px;
        @include mixins.flex-center-center;
        gap: 4px;
        font-weight: 500;
        font-size: 14px;
        @include mixins.hover {
            color: #2aabee;
        }
        @include mixins.desktop {
            font-size: 16px;
            margin-top: 8px;
        }
        &::before {
            content: "";
            width: 20px;
            height: 20px;
            mask: url(../img/icons/telegram.svg) center no-repeat;
            mask-size: contain;
            background-color: #2aabee;
        }
    }
    &__socials {
        margin-top: 32px;
        display: flex;
        flex-direction: column;
        gap: 16px;
        @include mixins.tablet {
            flex-direction: row;
            gap: 24px;
            flex-wrap: wrap;
        }
    }
    &__social {
        border-radius: 4px;
        background-color: var.$accent;
        @include mixins.flex-center-center;
        gap: 12px;
        padding: 12px 20px 12px 12px;
        color: var.$text-dark;
        font-weight: 500;
        font-size: 14px;
        @include mixins.hover {
            background-color: var.$hover-primary;
        }
        &::before {
            content: "";
            width: 20px;
            height: 20px;
            background: url(../img/icons/logo-circle.svg) center no-repeat;
        }
        &--tg {
            background-color: var.$bg;
            color: var.$text-light;
            @include mixins.hover {
                background-color: var.$bg;
                color: var.$accent;
                &::before {
                    background: var.$accent;
                }
            }
            &::before {
                mask: url(../img/icons/telegram.svg) center no-repeat;
                mask-size: contain;
                background: var.$text-secondary;
                transition: all 0.1s linear;
            }
        }
        &--x {
            background-color: var.$bg;
            color: var.$text-light;
            @include mixins.hover {
                background-color: var.$bg;
                color: var.$accent;
                &::before {
                    background: var.$accent;
                }
            }
            &::before {
                mask: url(../img/icons/xtwitter.svg) center no-repeat;
                mask-size: contain;
                background: var.$text-secondary;
                transition: all 0.1s linear;
            }
        }
    }
}