@use "../../commons/var";
@use "../../commons/mixins";

.about-what {
    margin-top: 64px;
    @include mixins.tablet {
        margin-top: 96px;
    }
    @include mixins.desktop {
        margin-top: 120px;
        @include mixins.flex-center;
        gap: 88px;
    }
    &__title {
        font-weight: 700;
        font-size: 24px;
        @include mixins.tablet {
            font-size: 40px;
        }
    }
    &__text {
        margin-top: 24px;
        @include mixins.tablet {
            margin-top: 32px;
        }
    }
    &__list {
        margin-top: 24px;
    }
    &__item {
        color: var.$text-primary;
        position: relative;
        padding-left: 20px;
        &::before {
            content: '';
            position: absolute;
            top: 10px;
            left: 8px;
            width: 3px;
            height: 3px;
            background-color: var.$text-light;
            border-radius: 50%;
        }
        & span {
            color: var.$text-light;
        }
    }
    &__image {
        margin-top: 32px;
        padding-right: 20px;
        padding-bottom: 20px;
        background-image: url(../img/about/dashboard-bg.png);
        background-repeat: no-repeat;
        background-position: right bottom;
        background-size: 90%;
        & img {
            width: 100%;
        }
        @include mixins.tablet {
            margin-top: 40px;
            padding-right: 40px;
            padding-bottom: 40px;
        }
        @include mixins.desktop {
            margin-top: 0;
            flex: 0 0 588px;
            background-size: 85%;
        }
    }
}