@use "../../commons/var";
@use "../../commons/mixins";

.solution {
    padding-bottom: 64px;
    @include mixins.tablet {
        padding-bottom: 96px;
    }
    &__box {
        border: 1px solid var.$border;
        border-radius: 24px;
        overflow: hidden;
        box-shadow: 0px 10px 50px 0px rgba(0, 0, 0, 0.1);
        @include mixins.tablet {
            border-radius: 32px;
        }
    }
    &__head {
        padding: 32px;
        background-color: var.$bg-secondary;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            width: 1120px;
            height: 876px;
            top: -500px;
            left: -420px;
            background-image: url(../img/index/solution-bg.svg);
            background-size: contain;
            background-repeat: no-repeat;
            pointer-events: none;
        }
        @include mixins.tablet {
            padding: 48px;
            &::before {
                left: auto;
                right: -420px;
                z-index: var.$ui-index-1;
            }
            &::after {
                content: '';
                position: absolute;
                width: 346px;
                height: 306px;
                top: calc(50% - 153px);
                left: auto;
                right: 16px;
                background-image: url(../img/index/solution.svg);
                background-size: contain;
                background-repeat: no-repeat;
                pointer-events: none;
                z-index: var.$ui-index-2;
                pointer-events: none;
            }
        }
        @include mixins.desktop {
            padding: 96px 56px;
            &::after {
                display: none;
            }
        }
    }
    &__head-imgs {
        display: none;
        @include mixins.desktop {
            position: absolute;
            display: block;
            width: 500px;
            height: 438px;
            top: 40px;
            right: 60px;
            & .laptop {
                position: absolute;
                right: 28px;
                top: 3px;
            }
            & .circle {
                position: absolute;
                top: 24px;
                left: 20px;
                transition: transform 2s ease-out;
            }
            & .money {
                position: absolute;
                bottom: 0;
                right: 0;
                transition: transform 2s ease-out;
            }
        }
    }
    &__title {
        font-family: var.$roboto;
        line-height: 1.2;
        font-weight: 700;
        font-size: 32px;
        & span {
            color: var.$accent;
        }
        @include mixins.tablet {
            font-size: 40px;
            max-width: 420px;
        }
        @include mixins.desktop {
            font-size: 48px;
            max-width: 560px;
        }
    }
    &__text {
        margin-top: 16px;
        color: var.$text-primary;
        @include mixins.tablet {
            font-size: 18px;
        }
        @include mixins.desktop {
            margin-top: 32px;
            max-width: 500px;
        }
    }
    &__link {
        margin-top: 32px;
        width: 100%;
        display: block;
        font-weight: 600;
        display: flex;
        justify-content: center;
        align-items: center;
        align-self: flex-start;
        gap: 16px;
        background-color: var.$accent;
        color: var.$text-dark;
        border-radius: 8px;
        padding: 16px;
        white-space: nowrap;
        @include mixins.hover {
            background-color: var.$hover-primary;
        }
        @include mixins.tablet {
            margin-top: 40px;
            width: auto;
            display: inline-flex;
        }
        @include mixins.desktop {
            padding: 16px 23px 16px 15px;
        }
        &::before {
            content: "";
            width: 22px;
            height: 22px;
            mask-image: url(../img/icons/telegram.svg);
            mask-position: center;
            mask-repeat: no-repeat;
            mask-size: contain;
            background-color: var.$text-dark;
        }
    }
    &__inner {
        padding: 40px 32px;
        @include mixins.tablet {
            padding: 48px;
        }
        @include mixins.desktop {
            padding: 56px;
            display: flex;
            gap: 40px;
        }
    }
    &__description {
        @include mixins.desktop {
            margin-top: 48px;
        }
    }
    &__switches {
        border: 1px solid var.$border;
        @include mixins.flex-center;
        flex-direction: column;
        gap: 4px;
        padding: 4px;
        border-radius: 12px;
        @include mixins.tablet {
            display: inline-flex;
            flex-direction: row;
        }
    }
    &__switch {
        opacity: 0;
        position: absolute;
        z-index: -1;
        & + label {
            font-weight: 600;
            font-size: 14px;
            color: var.$text-primary;
            padding: 8px;
            width: 100%;
            text-align: center;
            cursor: pointer;
            border-radius: 8px;
            @include mixins.hover {
                color: var.$accent-secondary;
            }
            @include mixins.tablet {
                width: auto;
                padding: 12px 20px;
                font-size: 16px;
            }
        }
        &:checked + label {
            background-color: var.$accent-secondary;
            cursor: default;
            pointer-events: none;
            color: var.$text-dark;
        }
    }
    &__steps {
        margin-top: 32px;
        &.hide {
            display: none;
        }
    }
    &__step {
        display: flex;
        gap: 8px;
        color: var.$text-light;
        cursor: pointer;
        padding: 8px 0;
        @include mixins.tablet {
            padding: 12px 0;
        }
        & p {
            transition: color 0.2s ease-in-out;
            font-weight: 600;
        }
        &.gray p {
            color: var.$text-secondary;
        }
        &.active p {
            color: var.$text-light;
        }
        & span {
            color: var.$text-secondary;
            white-space: nowrap;
        }
    }
}
