@use "../commons/var";
@use "../commons/mixins";

.cookie {
    box-shadow: 0px 10px 10px 0px #00000038, 0px 14px 28px 0px #00000040;
    border-radius: 8px;
    background-color: var.$accent-secondary;
    position: fixed;
    bottom: 24px;
    left: 50%;
    transform: translateX(-50%);
    z-index: var.$ui-index-10;
    opacity: 0;
    width: 342px;
    max-width: calc(100vw - 32px);
    transition: opacity .4s ease-out;
    display: none;
    @include mixins.tablet {
        width: 720px;
        max-width: 720px;
    }
    @include mixins.desktop {
        width: 880px;
        max-width: 880px;
    }
    &.show {
        display: block;
    }
    &.opacity {
        opacity: 1;
    }
    &-inner {
        padding: 16px;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        gap: 16px;
        @include mixins.tablet {
            flex-wrap: nowrap;
        }
    }
    &__text {
        width: 100%;
        color: var.$text-dark;
        font-size: 12px;
        @include mixins.tablet {
            order: 2;
            font-size: 14px;
        }

    }
    &__img {
        width: 33px;
        margin-left: auto;
        @include mixins.tablet {
            width: 40px;
            flex: 0 0 40px;
            order: 1;
        }
        & img {
            width: 100%;
        }
    }
    &__btn {
        border-radius: 4px;
        background-color: var.$black;
        padding: 8px;
        min-width: 96px;
        border: 0;
        font-size: 12px;
        line-height: 1.4;
        font-weight: 600;
        color: var.$text-light;
        cursor: pointer;
        @include mixins.hover {
            background-color: var.$hover-additional;
            color: var.$text-secondary;
        }
        @include mixins.tablet {
            order: 3;
            font-size: 14px;
            padding: 10px;
        }
    }
}