@use "../../commons/var";
@use "../../commons/mixins";

.referral {
    padding-bottom: 64px;
	@include mixins.tablet {
        padding-bottom: 96px;
	}
	@include mixins.desktop {
        padding-bottom: 120px;
	}
    &-inner {
        padding: 31px 31px 196px 31px;
        border: 1px solid var.$border;
        position: relative;
        border-radius: 32px;
        box-shadow: 0px 10px 50px 0px rgba(0, 0, 0, 0.1);
        @include mixins.tablet {
            padding: 39px;
        }
        &::after {
            position: absolute;
            content: '';
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border-radius: 32px;
            background-color: var.$bg-secondary;
            background-image: url(../img/referral/shadow.svg);
            background-size: contain;
            background-repeat: no-repeat;
            background-position: right bottom;
            z-index: var.$ui-index-2;
            pointer-events: none;
            @include mixins.tablet {
                background-image: url(../img/referral/shadow-tablet.svg);
            }
        }
        & * {
            position: relative;
            z-index: var.$ui-index-3;
        }
    }
    &__title {
        font-family: var.$roboto;
        font-weight: 700;
        font-size: 32px;
        @include mixins.tablet {
            font-size: 40px;
        }
    }
    &__text {
        margin-top: 26px;
        color: var.$text-primary;
        @include mixins.tablet {
            margin-top: 16px;
            font-size: 20px;
            max-width: 418px;
        }
        @include mixins.desktop {
            max-width: 699px;
        }
        & a {
            color: var.$accent;
            @include mixins.hover {
                color: var.$hover-primary;
            }
        }
    }
    &__percent {
        position: absolute;
        z-index: var.$ui-index-3;
        right: 0;
        bottom: -56px;
        font-size: 180px;
        font-weight: 800;
        color: var.$accent;
    }
}