@use "../commons/var";
@use "../commons/mixins";

.banner {
    background-size: 0;

    @include mixins.tablet {
        min-height: 360px;
        padding: 64px;
        border-radius: 8px;
        @include mixins.flex-column-space;
        background-size: cover;
        background-position: top left;
        border: 1px solid var.$border;
        .about & {
            background-image: url(../img/about/banner.png);
        }
        .contact & {
            background-image: url(../img/contact/banner-bg.png);
        }
        .referral-page & {
            background-image: url(../img/referral/banner.png);
            @include mixins.desktop {
                &::before {
                    pointer-events: none;
                    content: "";
                    position: absolute;
                    width: 442px;
                    height: 442px;
                    bottom: -20px;
                    right: 0;
                    background: url(../img/referral/banner-shadow.svg) center
                        no-repeat;
                    background-size: contain;
                    z-index: var.$ui-index-1;
                }
                &::after {
                    content: "8%";
                    font-size: 220px;
                    font-weight: 800;
                    color: var.$accent;
                    position: absolute;
                    bottom: -70px;
                    right: 0;
                }
            }
        }
    }
    @include mixins.desktop {
        min-height: 420px;
        background-repeat: no-repeat;
        background-size: calc(100% + 48px);
        position: relative;
        overflow: hidden;
        border-radius: 8px;
    }
    & .breadcrumbs {
        margin-top: 24px;
    }
    &-header {
        @include mixins.tablet {
            @include mixins.flex-center-space;
        }
        & .back {
            background-color: var.$accent-secondary;
            border: 1px solid var.$accent-secondary;

            color: var.$text-dark;
            &::before {
                background-color: var.$text-dark;
            }
            @include mixins.hover {
                background-color: var.$hover-secondary;
                border: 1px solid var.$hover-secondary;
            }
        }
    }
    &__title {
        margin-top: 8px;
        font-weight: 700;
        font-size: 24px;
        & span {
            color: var.$accent;
        }
        @include mixins.tablet {
            font-size: 32px;
        }
    }
    &__text {
        margin-top: 32px;
        font-weight: 600;
        & span {
            color: var.$accent;
        }
        @include mixins.tablet {
            margin-top: 24px;
            max-width: 424px;
        }
    }
    &__logo {
        display: none;
        @include mixins.tablet {
            display: block;
        }
    }
    &__subtext {
        margin-top: 32px;
        @include mixins.tablet {
            margin-top: 64px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 24px;
        }
        & p {
            max-width: 524px;
        }
        & a {
            margin-top: 32px;
            border: 1px solid var.$accent;
            background-color: var.$accent;
            display: inline-flex;
            align-items: center;
            gap: 8px;
            font-weight: 500;
            color: var.$text-dark;
            border-radius: 4px;
            padding: 16px 42px;
            white-space: nowrap;
            transition: border 0.2s ease-in-out,
            background-color 0.2s ease-in-out;
            @include mixins.tablet {
                padding: 16px 23px 16px 17px;
                margin-top: 0;
            }
            &::before {
                content: "";
                width: 20px;
                height: 20px;
                background-color: var.$text-dark;
                mask: url(../img/icons/energy.svg) center no-repeat;
            }
            @media (hover: hover) {
                &:hover {
                    border: 1px solid var.$hover-primary;
                    background-color: var.$hover-primary;
                }
            }
        }
    }
}
