@use "../commons/var";
@use "../commons/mixins";

.pricing {
    padding: 40px 0 64px;
    @include mixins.tablet {
        padding: 40px 0 96px;
    }
    @include mixins.desktop {
        padding: 40px 0 120px;
    }
    &-header {
        display: flex;
        flex-direction: column;
        margin-top: 8px;
        @include mixins.tablet {
            flex-direction: row;
            align-items: center;
            gap: 16px;
        }
    }
    &__title {
        font-weight: 700;
        font-size: 24px;
        @include mixins.tablet {
            font-size: 32px;
            margin-right: auto;
        }
    }
    &__currencies {
        order: 3;
        margin-top: 16px;
        border: 1px solid var.$border;
        border-radius: 8px;
        padding: 3px;
        @include mixins.flex-center;
        gap: 4px;
        @include mixins.tablet {
            order: 2;
            margin-top: 0;
        }
    }
    &__currency {
        background-color: transparent;
        border: 0;
        font-size: 14px;
        font-weight: 700;
        line-height: 1.4;
        color: var.$text-light;
        width: calc(33.333% - 3px);
        padding: 6px;
        border-radius: 4px;
        cursor: pointer;
        @include mixins.hover {
            background-color: var.$bg-secondary;
        }
        @include mixins.tablet {
            width: auto;
            padding: 6px 10px;
        }
        &.active {
            background-color: var.$accent-secondary;
            color: var.$text-dark;
            pointer-events: none;
        }
    }
    &__link {
        display: block;
        text-align: center;
        margin-top: 24px;
        width: 100%;
        padding: 10px 12px;
        color: var.$text-dark;
        font-size: 14px;
        font-weight: 500;
        background-color: var.$accent;
        border-radius: 8px;
        order: 2;
        @include mixins.tablet {
            margin-top: 0;
            width: auto;
            padding: 10px 16px;
        }
        @include mixins.hover {
            background-color: var.$hover-primary;
        }
    }
    &-content {
        margin-top: 16px;
        padding-bottom: 40px;
    }
    &__tabs {
        margin-top: 16px;
        border: 1px solid var.$border;
        border-radius: 16px;
        padding: 7px;
        display: flex;
        flex-direction: column;
        gap: 8px;
        @include mixins.tablet {
            margin-top: 24px;
            flex-direction: row;
        }
    }
    &__tab {
        @include mixins.flex-center-center;
        gap: 8px;
        padding: 10px;
        text-align: center;
        font-weight: 600;
        font-size: 14px;
        line-height: 1.4;
        color: var.$text-primary;
        text-align: center;
        border: 0;
        border-radius: 8px;
        background-color: transparent;
        cursor: pointer;
        @include mixins.hover {
            background-color: var.$bg-secondary;
        }
        @include mixins.tablet {
            width: calc(50% - 4px);
        }
        &::before {
            content: "";
            width: 16px;
            height: 16px;
            @include mixins.svg-mask("energy");
            background-color: var.$accent;
        }
        &--smart::before {
            mask-image: url(../img/icons/component.svg);
        }
        &.active {
            background-color: var.$accent;
            color: var.$text-dark;
            pointer-events: none;
            &::before {
                background-color: currentColor;
            }
        }
    }
    &__text {
        text-align: center;
        margin-top: 14px;
        color: var.$text-secondary;
        font-size: 14px;
        @include mixins.tablet {
            margin-top: 24px;
            text-align: left;
        }
    }
    &__plans {
        margin-top: 32px;
        &.hide {
            display: none;
        }
    }
    &__table {
        margin-top: 32px;
        &-inner {
            overflow: hidden;
            display: flex;
            gap: 24px;
            position: relative;
            &::before {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background-color: var.$bg;
                z-index: var.$ui-index-2;
            }
            &::after {
                content: '';
                position: absolute;
                top: 0;
                left: 0;
                width: 30px;
                height: 30px;
                border-radius: 50%;
                z-index: var.$ui-index-3;
                border: 2px solid var.$border;
                border-top: 2px solid var.$accent;
                position: absolute;
                top: calc(50% - 15px);
                left: calc(50% - 15px);
                animation: spin 1s linear infinite;
            }
            &.loaded {
                transition: 1s;
                &::before, &::after {
                    display: none;
                }
            }
        }
        &-scroll {
            overflow-x: auto;
            @include mixins.scrollbar;
            display: grid;
            grid-template-columns: 214px 208px 208px 208px 208px;
            width: 1046px;
        }
        &-col {
            margin-bottom: 16px;
            text-align: center;
            padding-top: 8px;
            border-radius: 16px;
            transition: all .15s linear;
            @include mixins.hover {
                transition: all .15s linear;
                background: #FFFFFF1A;
                & .pricing__table-name {
                    color: var.$text-dark;
                    box-shadow: 0px 10px 50px 0px #0000001A;
                    background-color: var.$text-light;
                }
            }
            &--fixed {
                text-align: right;
                font-weight: 700;
                flex: 0 0 130px;
                & .pricing__table-name {
                    padding-inline: 0;
                    margin: 0;
                }
                & .pricing__table-value {
                    padding-bottom: 16px;
                    padding-inline: 0;
                    border-bottom: 0;
                }
                @include mixins.hover {
                    background: transparent;
                    transition: all .15s linear;
                    & .pricing__table-name {
                        color: var.$text-secondary;
                        box-shadow: initial;
                        background-color: var.$bg;
                    }
                }
            }
            &--fill {
                background-color: var.$accent;
                color: var.$text-dark;
                font-weight: 700;
                padding-inline: 8px;
                margin-right: 24px;
                @include mixins.hover {
                    transition: all .15s linear;
                    background: var.$accent;
                }
                & .pricing__table-name {
                    margin: 0;
                }
                & .pricing__table-value {
                    border-bottom: 1px solid #1010101A;
                    font-weight: 700;
                }
            }
        }
        &-name {
            font-weight: 700;
            font-size: 12px;
            margin: 0 8px;
            color: var.$text-secondary;
            padding: 8px;
            border-radius: 8px;
            background-color: var.$bg;
            transition: all .15s linear;
        }
        &-value {
            padding: 16px 32px 15px;
            border-bottom: 1px solid #FFFFFF1A;
            font-size: 14px;
            text-transform: uppercase;
            &:last-child {
                border-bottom: 0;
            }
        }
        &-hover {
            position: absolute;
            background: #FFFFFF1A;
            border-radius: 8px;
            width: 100%;
            height: 52px;
            pointer-events: none;
            opacity: 0;
            transition: all .15s linear;
            &.visible {
                transition: all .15s linear;
                opacity: 1;
            }
        }
    }
    &__steps {
        display: flex;
        flex-direction: column;
        gap: 24px;
        margin-top: 40px;
        @include mixins.desktop {
            flex-direction: row;
        }
        &.hide {
            display: none;
        }
    }
    &__step {
        border-radius: 16px;
        border: 1px solid var.$border;
        padding: 15px 15px 23px;
        @include mixins.desktop {
            flex: 0 0 282px;
            padding: 15px 15px 10px;
        }
        &:first-child {
            border: 1px solid var.$accent;
        }
        &:nth-child(2) {
            flex: 0 0 588px;
        }
        &-number {
            display: flex;
            align-items: center;
            border-radius: 8px;
            background-color: var.$accent;
            color: var.$text-dark;
            font-size: 14px;
            & span {
                font-weight: 700;
                padding: 8px 4px;
                flex: 0 0 36px;
                text-align: center;
                border-right: 2px solid currentColor;
            }
            & b {
                text-align: center;
                width: 100%;
                padding: 8px;
            }
        }
        &-blocks {
            @include mixins.tablet {
                display: flex;
                align-items: stretch;
                gap: 16px;
            }
            & .pricing__step-block {
                background-color: var.$bg-secondary;
                border-radius: 16px;
                padding: 16px;
                @include mixins.tablet {
                    background-color: transparent;
                    padding: 0;
                    width: calc(50% - 8px);
                }
            }
        }
        &-block {
            margin-top: 16px;
            @include mixins.desktop {
                flex-grow: 1;
            }
        }
        &-price {
            padding: 11px;
            border: 1px solid var.$border;
            border-radius: 8px;
            text-align: center;
            font-size: 20px;
            font-weight: 700;
        }
        &-energy {
            margin-top: 20px;
            font-size: 14px;
            @include mixins.flex-center-center;
            gap: 8px;
            &::before {
                content: "";
                width: 20px;
                height: 20px;
                background-image: url("../img/icons/energy-color.png");
                background-position: center;
                background-repeat: no-repeat;
                background-size: contain;
            }
        }
        &-text {
            margin-top: 16px;
            padding-top: 16px;
            border-top: 1px solid #ffffff1a;
            text-align: center;
            color: var.$text-primary;
            font-size: 14px;
        }
    }
    &-footer {
        border-top: 1px solid var.$border;
        padding-top: 40px;
        @include mixins.tablet {
            display: flex;
            gap: 52px;
        }
        @include mixins.desktop {
            gap: 10px;
        }
        &__title {
            font-weight: 700;
            font-size: 28px;
            @include mixins.tablet {
                flex: 0 0 248px;
            }
            @include mixins.desktop {
                flex: 0 0 336px;
            }
            & span {
                display: block;
                color: var.$accent;
            }
        }
        &__contacts {
            margin-top: 24px;
            border-radius: 16px;
            background-color: var.$bg-secondary;
            padding: 24px;
            @include mixins.tablet {
                margin-top: 0;
                align-self: flex-start;
                padding: 16px 24px;
            }
            @include mixins.desktop {
                display: flex;
                gap: 16px;
            }
        }
        &__text {
            font-weight: 700;
        }
        &__links {
            margin-top: 24px;
            display: flex;
            flex-direction: column;
            gap: 8px;
            @include mixins.tablet {
                flex-direction: row;
                margin-top: 16px;
                gap: 16px;
            }
            @include mixins.desktop {
                margin-top: 0;
            }
        }
        &__link {
            padding: 11px;
            text-align: center;
            font-weight: 500;
            font-size: 14px;
            line-height: 1.4;
            color: var.$text-primary;
            border-radius: 8px;
            border: 1px solid var.$border;
            @include mixins.hover {
                color: var.$text-light;
            }
            @include mixins.tablet {
                min-width: 160px;
            }
            &:first-child {
                background-color: var.$accent;
                color: var.$text-dark;
                @include mixins.hover {
                    background-color: var.$hover-primary;
                    color: var.$text-dark;
                }
            }
        }
    }
}
@keyframes spin {
    0% {
      transform: rotate(0deg);
      -webkit-transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
      -webkit-transform:rotate(360deg);
    }
  }
  