@use "../../commons/var";
@use "../../commons/mixins";

.schemes {
    display: none;
    @include mixins.tablet {
        display: block;
        margin: 64px auto 0;
        width: 608px;
    }
    @include mixins.desktop {
        flex: 0 0 608px;
        margin-top: 0;
        text-align: center;
    }
}

.scheme {
    &.hide {
        display: none;
    }
    & > div {
        transition: opacity .2s ease-in-out;
    }
    &.dull > div {
        opacity: 20%;
    }
    display: grid;
    grid-template-columns: 206px 190px 148px;
    grid-template-rows: 52px 116px 52px 140px;
    gap: 32px;
    & .lottie {
        display: block;
        width: 24px;
        height: 24px;
        & svg {
            width: 100%;
        }
    }
    & .lottie-big {
        display: block;
        width: 88px;
        height: 88px;
        & svg {
            width: 100%;
        }
    }
    &__incoming {
        grid-column: 2/3;
        position: relative;
        border: 1px solid var.$border;
        border-radius: 8px;
        padding: 16px;
        &::after {
            content: '';
            position: absolute;
            bottom: -32px;
            left: calc(50% - 5px);
            width: 10px;
            height: 32px;
            mask: url(../img/scheme/arrow-to-bottom.svg) center no-repeat;
            background-color: var.$border;
        }
        & p {
            max-width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
        }
        & span {
            position: absolute;
            padding: 0 11px;
            background-color: var.$bg-secondary;
            font-size: 12px;
            font-weight: 500;
            color: var.$text-secondary;
            top: 0;
            left: 50%;
            transform: translate(-50%, -50%);
            white-space: nowrap;
        }
    }
    &__service {
        position: relative;
        grid-row: 2/3;
        grid-column: 1/2;
        text-align: center;
        padding: 16px;
        background-color: var.$bg;
        border-radius: 8px;
        padding: 16px 8px;
        display: flex;
        flex-direction: column;
        margin-top: 28px;
        margin-right: 16px;
        &::after {
            content: '';
            position: absolute;
            bottom: calc(50% - 6px);
            right: -40px;
            width: 40px;
            height: 12px;
            mask: url(../img/scheme/arrow-to-right.svg) center no-repeat;
            background-color: var.$border;
        }
        & .lottie-big {
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate(-50%, -50%);
        }
        & span {
            font-weight: 600;
            font-size: 14px;
            margin-top: auto;
        }
    }
    &__address {
        position: relative;
        grid-row: 2/3;
        grid-column: 2/3;
        margin-top: 28px;
        background-color: var.$bg;
        border-radius: 8px;
        text-align: center;
        padding: 16px 8px;
        display: flex;
        flex-direction: column;
        &::after {
            content: '';
            position: absolute;
            bottom: -32px;
            left: calc(50% - 5px);
            width: 10px;
            height: 32px;
            mask: url(../img/scheme/line-to-bottom.svg) center no-repeat;
            background-color: var.$border;
        }
        & div {
            position: absolute;
            background-color: var.$bg;
            border: 1px solid var.$border;
            border-radius: 8px;
            top: 0;
            left: 50%;
            transform: translate(-50%, -50%);
            padding: 11px 12px 10px 15px;
            font-weight: 500;
            font-size: 12px;
            display: flex;
            align-items: center;
            gap: 8px;
            &::after {
                content: '';
                width: 16px;
                height: 16px;
                mask-image: url(../img/icons/copy.svg);
                mask-position: center;
                background-color: var.$text-primary;
            }
        }
        & b {
            margin-top: auto;
            font-weight: 600;
            font-size: 14px;
        }
        & span {
            font-size: 12px;
            color: var.$text-primary;
        }
    }
    &__energy {
        grid-row: 2/3;
        grid-column: 3/4;
        height: 52px;
        align-self: center;
        margin: 28px auto 0;
        padding: 13px 13px 13px 17px;
        background-color: var.$bg;
        border-radius: 8px;
        border: 1px solid var.$border;
        display: flex;
        align-items: center;
        gap: 7px;
        justify-content: center;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            bottom: calc(50% - 6px);
            left: -43px;
            width: 43px;
            height: 12px;
            mask: url(../img/scheme/arrow-to-left.svg) center no-repeat;
            background-color: var.$border;
        }
        &::after {
            content: '';
            position: absolute;
            bottom: -53px;
            left: calc(50% - 0.75px);
            width: 1.5px;
            height: 53px;
            mask: url(../img/scheme/line-to-bottom.svg) center;
            background-color: var.$border;
        }
    }
    &__transaction {
        grid-row: 3/4;
        grid-column: 2/3;
        padding: 11px 13px 13px 19px;
        border: 1px solid var.$border;
        border-radius: 8px;
        display: inline-flex;
        align-self: center;
        margin: 0 auto;
        align-items: center;
        gap: 10px;
        position: relative;
        &::after {
            content: '';
            position: absolute;
            bottom: -32px;
            left: calc(50% - 5px);
            width: 10px;
            height: 32px;
            mask: url(../img/scheme/arrow-to-bottom.svg) center no-repeat;
            background-color: var.$border;
        }
        & .lottie {
            margin-top: 2px;
            flex: 0 0 24px;
        }
    }
    &__start {
        grid-row: 3/4;
        grid-column: 3/4;
    }
    &__start {
        position: relative;
        & img {
            width: 100%;
        }
    }
    &__wallet {
        margin-top: 52px;
        position: relative;
        grid-row: 4/5;
        grid-column: 2/3;
        text-align: center;
        padding: 16px;
        background-color: var.$bg;
        border-radius: 8px;
        padding: 16px 8px;
        display: flex;
        flex-direction: column;
        &::before {
            content: '';
            position: absolute;
            bottom: 44px;
            left: -143px;
            width: 135px;
            height: 204px;
            mask: url(../img/scheme/arrow-double.svg) center no-repeat;
            mask-size: contain;
            background-color: var.$border;
        }
        & .lottie-big {
            position: absolute;
            top: 0;
            left: 50%;
            transform: translate(-50%, -50%);
        }
        & span {
            font-weight: 600;
            font-size: 14px;
            margin-top: auto;
        }
    }
    &--second {
        grid-template-columns: 190px 148px 190px;
        grid-template-rows: 50px 88px 100px;
        gap: 32px;
        height: 326px;
        align-content: center;
        .scheme__start {
            grid-row: 1/2;
            grid-column: 2/3;
            width: 148px;
            margin-left: 8px;
            position: relative;
            &::before {
                position: absolute;
                content: '';
                left: -135px;
                width: 127px;
                height: 58px;
                top: 26px;
                mask: url(../img/scheme/line-to-left-bottom.svg) center no-repeat;
                background-color: var.$border;
            }
        }
        .scheme__energy {
            margin-top: 0;
            grid-row: 2/3;
            grid-column: 1/2;
            margin-bottom: auto;
            &::before {
                display: none;
            }
            &::after {
                mask: url(../img/scheme/arrow-to-bottom.svg) center no-repeat;
                background-color: var.$border;
                height: 32px;
                width: 10px;
                bottom: -32px;
                left: calc(50% - 5px);
            }
        }
        .scheme__address {
            grid-row: 2/3;
            grid-column: 3/4;
            margin-top: 0;
            &::after {
                display: none;
            }
        }
        .scheme__wallet {
            grid-row: 3/4;
            grid-column: 1/2;
            margin-top: 12px;
            &::before {
                content: '';
                position: absolute;
                bottom: calc(50% - 6px);
                left: auto;
                right: -40px;
                width: 40px;
                height: 12px;
                mask: url(../img/scheme/arrow-to-right.svg) center no-repeat;
                background-color: var.$border;
            }
        }
        .scheme__incoming {
            margin-top: auto;
            margin-left: 14px;
            margin-bottom: 18px;
            grid-row: 3/4;
            grid-column: 2/3;
            width: 190px;
            &::after {
                content: '';
                right: -80px;
                left: auto;
                width: 80px;
                height: 80px;
                bottom: 26px;
                mask: url(../img/scheme/arrow-to-right-top.svg) center no-repeat;
                background-color: var.$border;
            }
        }
    }
}


.scheme.dull {
    &.step1 {
        & .scheme__service, & .scheme__address {
            opacity: 1;
        }
        & .scheme__service::after {
            background-color: var.$accent-secondary;
        }
    }
    &.step2 {
        & .scheme__incoming, & .scheme__address {
            opacity: 1;
        }
        & .scheme__incoming::after {
            background-color: var.$accent-secondary;
        }
    }
    &.step3 {
        & .scheme__energy, & .scheme__address, & .scheme__start {
            opacity: 1;
        }
        & .scheme__energy::after, & .scheme__energy::before {
            background-color: var.$accent-secondary;
        }
    }
    &.step4 {
        & .scheme__service, & .scheme__wallet, & .scheme__transaction, & .scheme__address {
            opacity: 1;
        }
        & .scheme__address::after, & .scheme__transaction::after, & .scheme__wallet::before {
            background-color: var.$accent-secondary;
        }
    }
}
.scheme--second.dull {
    &.step5 {
        & .scheme__start, & .scheme__energy, & .scheme__wallet {
            opacity: 1;
        }
        & .scheme__start::before, & .scheme__energy::after {
            background-color: var.$accent-secondary;
        }
        & .scheme__address, & .scheme__incoming {
            opacity: .2;
        }
    }
    &.step6 {
        & .scheme__address, & .scheme__wallet, & .scheme__incoming {
            opacity: 1;
        }
        & .scheme__wallet::before, & .scheme__incoming::after {
            background-color: var.$accent-secondary;
        }
    }
}