@use "../../commons/var";
@use "../../commons/mixins";

.price-chart {
    &__head {
        @include mixins.tablet {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 16px;
        }
    }
    &__name {
        @include mixins.flex-center;
        gap: 16px;
        & img {
            width: 34px;
            height: 34px;
        }
        & h2 {
            font-size: 24px;
            font-weight: 700;
        }
    }
    &__change {
        margin-top: 14px;
        @include mixins.tablet {
            margin-top: 0;
            grid-row: 2;
        }
        & b {
            padding: 3px 5px;
            border-radius: 4px;
            font-size: 14px;
            background-color: var.$accent;
            color: var.$text-dark;
            &.red {
                background-color: var.$red;
                color: var.$text-light;
            }
        }
        & span {
            padding-left: 8px;
            font-size: 14px;
            color: var.$text-primary;
        }
    }
    &__price {
        margin-top: 14px;
        font-size: 14px;
        color: var.$text-primary;
        @include mixins.tablet {
            grid-column: 2;
            grid-row: 2;
            margin-top: 0;
            margin-left: auto;
            align-self: flex-end;
        }
        & b {
            font-weight: 700;
            color: var.$text-light;
        }
    }
    &__rate {
        align-self: center;
        font-size: 14px;
        margin-top: 8px;
        & span {
            color: var.$text-primary;
        }
    }
    &__info {
        margin-top: 24px;
        @include mixins.tablet {
            @include mixins.flex-center-space;
            padding-bottom: 10px;
            border-bottom: 1px solid var.$border;
        }
    }
    &__switches {
        @include mixins.flex-center;
        gap: 16px;
        padding-bottom: 8px;
        border-bottom: 1px solid var.$border;
        @include mixins.tablet {
            padding-bottom: 0;
            border-bottom: 0;
        }
        & input {
            position: absolute;
            z-index: -1;
            &:checked + label {
                color: var.$text-light;
                position: relative;
                &::after {
                    content: '';
                    position: absolute;
                    left: 0;
                    bottom: -11px;
                    width: 100%;
                    height: 1px;
                    background-color: var.$accent;
                    @include mixins.tablet {
                        bottom: -13px;
                    }
                }
            }
        }
        & label {
            font-weight: 700;
            font-size: 14px;
            color: var.$text-secondary;
            cursor: pointer;
            @include mixins.hover {
                color: var.$text-light;
            }
        }
    }
    &__wrap {
        margin-top: 24px;
        height: 291px;
        @include mixins.tablet {
            height: 412px;
        }
        & div {
            height: 100%;
        }
    }
}
