@use "../commons/var";
@use "../commons/mixins";

.article {
    font-size: 14px;
    @include mixins.tablet {
        font-size: 16px;
    }
    &-page {
        padding-top: 40px;
    }
    &-inner {
        margin-top: 32px;
        padding-bottom: 64px;
        display: flex;
        flex-direction: column-reverse;
        @include mixins.tablet {
            padding-bottom: 96px;
        }
        @include mixins.desktop {
            padding-bottom: 120px;
            display: flex;
            flex-direction: row-reverse;
            gap: 48px;
        }
    }
    &-sidebar {
        margin-top: 16px;
        @include mixins.tablet {
            margin-top: 32px;
        }
        @include mixins.desktop {
            margin-top: 0;
            flex: 0 0 360px;
            position: sticky;
            align-self: flex-start;
            top: 20px;
            overflow-y: auto;
            max-height: calc(100vh - 15px);
        }
    }
    &__share {
        background: var.$bg-secondary;
        border: 1px solid var.$border;
        border-radius: 8px;
        padding: 23px;
        @include mixins.tablet {
            width: max-content;
            min-width: 348px;
        }
        @include mixins.desktop {
            width: 100%;
        }
        &-title {
            font-weight: 500;
        }
    }
    &__socials {
        margin-top: 16px;
        gap: 12px;
    }
    &__social {
        width: 32px;
        height: 32px;
    }
    &__link {
        margin-top: 20px;
        background-color: var.$bg-secondary;
        background-image: url(../img/article/logo-banner.png);
        background-size: 140px 202px;
        background-position: right bottom;
        background-repeat: no-repeat;
        border-radius: 8px;
        border: 1px solid var.$border;
        padding: 23px;
        display: flex;
        flex-direction: column;
        gap: 16px;
        @include mixins.tablet {
            display: none;
        }
        @include mixins.desktop {
            display: flex;
        }
        @include mixins.hover {
            & .article__link-start {
                color: var.$hover-primary;
                &::after {
                    background-color: var.$hover-primary;
                }
            }
        }
    }
    &__link-title {
        font-weight: 700;
        font-size: 20px;
        max-width: 196px;
        & span {
            color: var.$accent;
        }
    }
    &__link-text {
        max-width: 190px;
    }
    &__link-start {
        color: var.$accent;
        @include mixins.flex-center;
        gap: 4px;
        font-weight: 600;
        max-width: 196px;
        &::after {
            margin-top: 2px;
            content: "";
            width: 15px;
            height: 15px;
            mask: url(../img/icons/arrow.svg) center no-repeat;
            mask-size: contain;
            background-color: var.$accent;
            transform: rotate(90deg);
        }
    }
    &__titles {
        display: none;
        @include mixins.desktop {
            margin-top: 64px;
            display: block;
            position: relative;
        }
        &-item {
            position: relative;
            padding-left: 32px;
            @include mixins.hover {
                & a {
                    color: var.$accent;
                }
            }
            &::before {
                position: absolute;
                content: "";
                width: 1px;
                height: 100%;
                left: 10px;
                background-color: var.$border;
            }
            &:first-child::before {
                height: calc(100% - 3px);
                top: 3px;
            }
            &:last-child::before {
                height: 10px;
                top: 0;
            }
            &::after {
                position: absolute;
                content: "";
                width: 15px;
                height: 15px;
                left: 2px;
                top: 3px;
                border-radius: 50%;
                border: 1px solid var.$border;
                background-color: var.$bg;
            }
            &:not(:last-child) {
                padding-bottom: 24px;
            }
            &.active {
                &::after {
                    position: absolute;
                    content: "";
                    width: 8px;
                    height: 8px;
                    left: 2px;
                    top: 3px;
                    border-radius: 50%;
                    border: 4px solid var.$bg;
                    background-color: var.$accent;
                    outline: 1px solid var.$accent;
                }
                & a {
                    color: var.$text-light;
                }
            }
            & a {
                position: relative;
                font-size: 16px;
                font-weight: 600;
                color: var.$text-secondary;
            }
        }
    }
    & img {
        box-shadow: 0px 4px 12px 0px #00000033;
        border-radius: 8px;
    }
    &-text {
        margin-top: 16px;
        color: var.$text-primary;
        @include mixins.tablet {
            margin-top: 32px;
        }
        & *:first-child {
            margin-top: 0;
        }
    }
    &__image-caption {
        display: block;
        margin-top: 8px;
        color: var.$text-secondary;
        font-size: 10px;
        text-align: center;
        @include mixins.tablet {
            margin-top: 12px;
            font-size: 12px;
        }
    }
    & h1 {
        font-weight: 700;
        font-size: 24px;
        margin-top: 40px;
        @include mixins.tablet {
            font-size: 32px;
            margin-top: 64px;
        }
    }
    & h2 {
        font-weight: 700;
        font-size: 24px;
        @include mixins.tablet {
            font-size: 28px;
        }
    }
    & h3 {
        font-weight: 500;
        font-size: 22px;
        @include mixins.tablet {
            font-size: 24px;
        }
    }
    & h4 {
        font-weight: 500;
        font-size: 20px;
        @include mixins.tablet {
            font-size: 22px;
        }
    }
    & h5 {
        font-weight: 600;
        font-size: 18px;
        @include mixins.tablet {
            font-size: 20px;
        }
    }
    & h6 {
        font-weight: 500;
        font-size: 18px;
        @include mixins.tablet {
            font-size: 20px;
        }
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        margin-top: 16px;
        color: var.$text-light;
        @include mixins.tablet {
            margin-top: 24px;
        }
        & span {
            color: var.$accent;
        }
    }

    & p {
        margin-top: 16px;
        font-size: 14px;
        @include mixins.tablet {
            font-size: 16px;
            margin-top: 24px;
        }
    }
    & img {
        height: auto!important;
        &:not(:first-child) {
            margin-top: 40px;
            @include mixins.tablet {
                margin-top: 64px;
            }
        }
    }
    & .img-block {
        margin: 32px 0;
        & span {
            display: block;
            text-align: center;
            margin-top: 8px;
            color: var.$text-secondary;
            font-size: 10px;
            @include mixins.tablet {
                margin-top: 12px;
                font-size: 12px;
            }
        }
        & img {
            margin: 0 auto;
        }
    }
    & hr {
        margin: 32px 0;
        border: 0;
        border-bottom: 1px solid var.$border;
    }
    & table {
        margin-top: 40px;
        width: 100%;
        border-collapse: collapse;
        overflow: hidden;
        border-radius: 12px;
        @include mixins.tablet {
            margin-top: 64px;
        }
        & thead {
            background-color: var.$bg-secondary;
        }
        & thead th {
            padding: 15px;
            text-align: left;
            font-weight: 600;
            color: var.$text-primary;
        }
        & tbody tr {
            border-bottom: 1px solid var.$border;
        }
        & tbody tr:last-child {
            border-bottom: 0px solid var.$border;
        }
        & tbody tr td {
            padding: 15px;
        }
    }
    & b, & strong {
        font-weight: 600;
    }
    & ul,
    & ol {
        margin-top: 16px;
        @include mixins.tablet {
            margin-top: 24px;
        }
    }
    & ul li {
        list-style-type: disc;
        list-style-position: inside;
        position: relative;
        &:not(:first-child) {
            margin-top: 8px;
        }
    }
    & ol li {
        list-style-type: decimal;
        list-style-position: inside;
        &:not(:first-child) {
            margin-top: 8px;
        }
    }
    & blockquote {
        margin-top: 16px;
        padding: 15px;
        font-size: 16px;
        display: flex;
        gap: 16px;
        border-radius: 8px;
        background-color: var.$bg-secondary;
        border: 1px solid var.$border;
        color: var.$text-light;
        @include mixins.tablet {
            margin-top: 24px;
        }
        &::before {
            content: '';
            flex: 0 0 22px;
            width: 22px;
            height: 22px;
            background: url(../img/icons/info.svg) center no-repeat;
            background-size: cover;
        }
    }
    & a {
        color: var.$accent;
        text-underline-offset: 15%;
        text-decoration-thickness: 10%;
        @include mixins.hover {
            text-decoration: underline;
        }
    }
}