@use "../commons/var";
@use "../commons/mixins";

.blog {
    padding: 40px 0 64px;
    @include mixins.tablet {
        padding: 40px 0 96px;
    }
    @include mixins.desktop {
        padding: 40px 0 120px;
    }
    & .container {
        overflow: hidden;
    }
    &-header {
        margin-top: 8px;
        @include mixins.tablet {
            @include mixins.flex-center-space;
        }
    }
    &-header__link {
        @include mixins.flex-center;
        gap: 6px;
        font-weight: 600;
        color: var.$accent;
        margin-top: 16px;
        @include mixins.tablet {
            padding-bottom: 3px;
            align-self: flex-end;
        }
        @include mixins.desktop {
            padding-bottom: 5px;
        }
        @include mixins.hover {
            color: var.$hover-primary;
            &::after {
                background-color: var.$hover-primary;
            }
        }
        &::after {
            content: '';
            width: 16px;
            height: 16px;
            mask-image: url(../img/icons/link.svg);
            background-color: var.$accent;
        }
    }
    &__title {
        font-weight: 700;
        font-size: 24px;
        margin-top: 8px;
        @include mixins.tablet {
            font-size: 32px;
            margin-top: 0;
        }
    }
    &__socials {
        display: none;
        @include mixins.tablet {
            display: inline-flex;
            gap: 8px;
            color: var.$text-primary;
            align-self: flex-end;
            margin-bottom: 3px;
            & .socials {
                gap: 8px;
            }
            & .social {
                width: 24px;
                height: 24px;
            }
        }
    }
    &__categories {
        margin-top: 24px;
        padding: 16px 16px 8px 16px;
        border-radius: 8px;
        background-color: var.$bg-secondary;
        position: relative;
        @include mixins.desktop {
            margin-top: 32px;
        }
    }
    &__categories-inner {
        @include mixins.flex-center;
        gap: 8px;
        position: relative;
        overflow-x: auto;
        overscroll-behavior-x: contain;
        scroll-snap-type: x mandatory;
        overflow-y: hidden;
        left: 0px;
        padding-bottom: 8px;
        @include mixins.tablet {
            overflow: hidden;
            flex-wrap: wrap;
        }
    }
    &__category {
        scroll-snap-align: start;
        & a {
            padding: 11px 19px 11px 11px;
            @include mixins.flex-center;
            gap: 12px;
            border: 1px solid transparent;
            border-radius: 4px;
            font-size: 14px;
            font-weight: 500;
            color: var.$text-light;
            white-space: nowrap;
            @include mixins.hover {
                background-color: var.$hover-additional;
                border-color: var.$hover-additional;
            }
            &.active {
                background-color: var.$accent;
                border-color: var.$accent;
                color: var.$text-dark;
                cursor: default;
                pointer-events: none;
                & .icon {
                    background-color: currentColor;
                }
            }
        }
        & .icon {
            width: 20px;
            height: 20px;
            background-color: var.$text-secondary;
            mask-position: center;
            mask-repeat: no-repeat;
            transition: all 0.1s linear;
        }
    }
    &__articles {
        margin-top: 24px;
        & .blog__article-item:not(:first-of-type) .blog__article-img {
            display: none;
        }
        &--equally {
            & .blog__article-item:not(:first-of-type) .blog__article-img {
                display: block;
            }
        }
        @include mixins.tablet {
            margin-top: 32px;
            & .blog__article-item:first-child .blog__article-inner {
                padding: 0 16px 16px;
            }
            & .blog__article-item:not(:first-of-type) .blog__article {
                display: grid;
                grid-template-columns: 240px 1fr;
                align-items: center;
                gap: 24px;
            }
            & .blog__article-item:not(:first-of-type) .blog__article-inner {
                padding: 0;
            }
            & .blog__article-item:not(:first-of-type) .blog__article-title {
                font-size: 20px;
            }
            & .blog__article-item:not(:first-of-type) .blog__article-img {
                display: block;
                margin-bottom: 0;
            }
            &--equally {
                display: grid;
                grid-template-columns: 1fr 1fr;
                gap: 24px 20px;
                & .blog__article-item:not(:first-of-type) .blog__article {
                    display: block;
                }
                & .blog__article-item:not(:first-of-type) .blog__article-inner {
                    padding: 0 8px 8px;
                }
                & .blog__article-item:first-of-type .blog__article-inner {
                    padding: 0 8px 8px;
                }
                & .blog__article-item .blog__article-title {
                    font-size: 20px;
                }
                & .blog__article-item:not(:first-of-type) .blog__article-img {
                    margin-bottom: 24px;
                }
            }
        }
        @include mixins.desktop {
            display: grid;
            grid-template-columns: 588px 612px;
            grid-template-rows: repeat(3, 1fr);
            & .blog__article-item:first-child {
                grid-row: 1/4;
            }
            & .blog__article-item:not(:first-of-type) .blog__article {
                display: grid;
                grid-template-columns: 240px 1fr;
                align-items: center;
                gap: 24px;
            }
            &--equally {
                display: grid;
                grid-template-columns: 1fr 1fr 1fr;
                gap: 32px 24px;
                & .blog__article-item:first-child {
                    grid-row: 1;
                }
                & .blog__article-item:not(:first-of-type) .blog__article {
                    display: block;
                }
            }
        }
    }
    &__article {
        display: block;
        height: 100%;
        padding: 16px;
        overflow: hidden;
        @include mixins.hover {
            background-color: var.$hover-additional;
            border-radius: 8px;
            & .blog__article-title {
                text-decoration: underline;
            }
            & .blog__article-text {
                color: var.$text-light;
            }
        }
        &-inner {
            padding: 0 8px 8px 8px;
        }
        &-img {
            width: 100%;
            border-radius: 8px;
            overflow: hidden;
            margin-bottom: 24px;
            & img {
                object-fit: cover;
                width: 100%;
                height: 100%;
            }
        }
        &-title {
            margin-top: 16px;
            font-weight: 700;
            font-size: 20px;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
            transition: all 0.1s linear;
            @include mixins.tablet {
                font-size: 24px;
            }
        }
        &-info {
            @include mixins.flex-center;
            gap: 16px;
        }
        &-tag {
            color: var.$accent-secondary;
            font-weight: 500;
            font-size: 14px;
            border: 1px solid currentColor;
            border-radius: 4px;
            padding: 1px 7px;
            &.green {
                color: #74eb69;
            }
            &.orange {
                color: #ffaf1a;
            }
            &.blue {
                color: #62c9ff;
            }
            &.blue-purple {
                color: #b2b2ff;
            }
            &.red {
                color: #ff6e6e;
            }
            &.violet {
                color: #ce8dff;
            }
            &.yellow {
                color: #ffd64f;
            }
        }
        &-date {
            font-size: 14px;
            color: var.$text-secondary;
        }
        &-text {
            margin-top: 16px;
            font-size: 14px;
            color: var.$text-secondary;
            display: -webkit-box;
            -webkit-line-clamp: 3;
            -webkit-box-orient: vertical;
            overflow: hidden;
            text-overflow: ellipsis;
            transition: all 0.1s linear;
            @include mixins.tablet {
                font-size: 16px;
            }
        }
    }
}
