.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100dvh;
  height: 100vh;
  padding: 40px 16px;
  z-index: 4;
  display: flex;
  align-items: center;
  justify-content: center;
}
.popup::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(23, 23, 23, 0.8);
}
.popup.hide {
  display: none;
}
.popup.block {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.popup.block::before {
  animation: growBefore 0.4s cubic-bezier(0, 0, 0, 1);
}
.popup.block .popup-inner {
  animation: grow 0.4s cubic-bezier(0, 0, 0, 1);
}
.popup.decrease::before {
  animation: decreaseBefore 0.4s ease-out;
}
.popup.decrease .popup-inner {
  animation: decrease 0.4s ease-out;
}
.popup__close {
  width: 24px;
  height: 24px;
  mask: url("../img/icons/close.svg") no-repeat;
  mask-position: center;
  mask-size: 100%;
  background-color: #b3b3b3;
  cursor: pointer;
  user-select: none;
}
