  padding: 0 24px;
}
.popup-inner {
  width: 100%;
  max-width: 640px;
  height: 100%;
  max-height: 400px;
  color: #b3b3b3;
  transition: top 0.4s cubic-bezier(0, 0, 0, 1);
  position: relative;
  border-radius: 16px;
  border: 1px solid #353535;
  background-color: #0B0B0B;
  box-shadow: 0px 3px 8px 0px rgba(0, 0, 0, 0.2392156863);
  overflow: hidden;
}
.popup-inner::after {
  content: "";
  position: absolute;
  width: calc(100% - 32px);
  height: 28px;
