    rotate: -15deg;
    top: 0px;
  }
}
@keyframes swapTrxDisable {
  0% {
    z-index: 10;
    rotate: 0deg;
    top: 0;
    left: 0;
  }
  50% {
    z-index: 10;
    rotate: 3deg;
    top: -4px;
