@use "../commons/var";
@use "../commons/mixins";

.contact {
    padding: 40px 0 64px;
    @include  mixins.tablet {
        padding: 24px 0 96px;
    }
    @include  mixins.desktop {
        padding: 40px 0 120px;
    }
    &-list {
        margin-top: 32px;
        display: flex;
        flex-direction: column;
        gap: 16px;
        @include  mixins.tablet {
            margin-top: 48px;
            flex-direction: row;
            flex-wrap: wrap;
            gap: 24px;
        }
        @include  mixins.desktop {
            margin-top: 24px;
        }
    }
    &__item {
        border-radius: 8px;
        background-color: var.$bg-secondary;
        border: 1px solid var.$border;
        padding: 23px;
        @include  mixins.tablet {
            padding: 31px;
            width: calc(50% - 12px);
            display: flex;
            flex-direction: column;
            &:nth-child(3n) {
                width: 100%;
            }
        }
        @include  mixins.desktop {
            position: relative;
            padding: 39px;
            width: calc(33.33333% - 16px);
            overflow: hidden;
            &:nth-child(3n) {
                width: calc(33.33333% - 16px);
            }
            &:nth-child(n + 4) {
                width: calc(50% - 12px);
            }
        }
    }
    &__title {
        font-weight: 700;
        font-size: 20px;
    }
    &__text {
        margin-top: 16px;
        color: var.$text-primary;
        flex-grow: 1;
    }
    &__link {
        position: relative;
        display: inline-flex;
        align-self: flex-start;
        align-items: center;
        gap: 8px;
        margin-top: 32px;
        padding: 3px 23px 3px 3px;
        border: 1px solid var.$border;
        border-radius: 24px;
        font-size: 14px;
        font-weight: 500;
        transition: all .2s ease-in-out;
        @media (hover: hover) {
            &:hover {
                background-color: var.$border;
            }
        }
        &::before {
            content: '';
            width: 32px;
            height: 32px;
            border-radius: 50%;
            background-color: var.$border;
            background-image: url(../img/icons/telegram.svg);
            background-size: 20px;
            background-repeat: no-repeat;
            background-position: center;
        }
        &::after {
            content: '';
            position: absolute;
            width: 20px;
            height: 20px;
            left: 9px;
            top: calc(50% - 10px);
            mask-image: url(../img/icons/telegram.svg);
            mask-size: 20px;
            mask-repeat: no-repeat;
            mask-position: center;
            background-color: var.$white;
        }
        &--accent {
            border: 1px solid var.$accent;
            background-color: var.$accent;
            color: var.$text-dark;
            @media (hover: hover) {
                &:hover {
                    border: 1px solid var.$hover-primary;
                    background-color: var.$hover-primary;
                }
            }
            &::before {
                background-color: var.$text-dark;
            }
            &::after {
                background-color: var.$accent;
            }
        }
        &--accent2 {
            border: 1px solid var.$accent-secondary;
            background-color: var.$accent-secondary;
            color: var.$text-dark;
            @media (hover: hover) {
                &:hover {
                    color: var.$text-light;
                    border: 1px solid var.$bg;
                    background-color: var.$bg;
                }
            }
            &::before {
                background-color: var.$text-dark;
            }
            &::after {
                background-color: var.$accent-secondary;
            }
        }
    }
}