@use "../commons/var";
@use "../commons/mixins";

@use "price/_chart";
@use "price/_description";
@use "price/_banner";
@use "price/_survey";
@use "price/_info";

.price {
    &-page {
        padding-top: 40px;
    }
    &-inner {
        margin-top: 24px;
        @include mixins.desktop {
            display: grid;
            grid-template-columns: 820px 356px;
            gap: 0 24px;
        }
    }
    &-sidebar {
        @include mixins.desktop {
            grid-column: 2;
            grid-row: 1/3;
        }
    }
}
