@use "../commons/var" as v;
@use "../commons/mixins" as m;

@keyframes grow {
    0% {
        top: 100%;
    }
    100% {
        top: 0;
    }
}
@keyframes growBefore {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes decrease {
    0% {
        top: 0;
    }
    100% {
        top: 100%;
    }
}
@keyframes decreaseBefore {
    0% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
.popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100dvh;
    height: 100vh;
    padding: 40px 16px;
    z-index: v.$ui-index-4;
    @include m.flex-center-center;
    &::before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(23, 23, 23, .8);
    }
    &.hide {
        display: none;
    }
    &.block {
        display: flex;
        flex-direction: column;
        justify-content: center;
        &::before {
            animation: growBefore 0.4s cubic-bezier(0, 0, 0, 1);
        }
        & .popup-inner {
            animation: grow 0.4s cubic-bezier(0, 0, 0, 1);
        }
    }
    &.decrease {
        &::before {
            animation: decreaseBefore 0.4s ease-out;
        }
        & .popup-inner {
            animation: decrease 0.4s ease-out;
        }
    }
    &__close {
        width: 24px;
        height: 24px;
        mask: url("../img/icons/close.svg") no-repeat;
        mask-position: center;
        mask-size: 100%;
        background-color: v.$text-primary;
        cursor: pointer;
        user-select: none;
        @include m.hover {
            background-color: v.$text-light;
        }
        & + * {
            padding: 0 24px;
        }
    }
    &-inner {
        width: 100%;
        max-width: 640px;
        height: 100%;
        max-height: 400px;
        color: v.$text-primary;
        transition: top 0.4s cubic-bezier(0, 0, 0, 1);
        position: relative;
        border-radius: 16px;
        border: 1px solid v.$border;
        background-color: v.$bg;
        box-shadow: 0px 3px 8px 0px #0000003d;
        overflow: hidden;
        &::after {
            content: '';
            position: absolute;
            width: calc(100% - 32px);
            height: 28px;
            bottom: 0;
            left: 16px;
            background: linear-gradient(360deg, #121212 0%, rgba(18, 18, 18, 0) 100%);
            @include m.desktop {
                width: calc(100% - 48px);
                left: 24px;
            }
        }
    }
    &-inner__head {
        @include m.flex-center-space;
        padding: 16px 16px 0;
        @include m.desktop {
            padding: 24px 24px 0;
        }
    }
    &-inner__wrap {
        display: flex;
        flex-direction: column;
        margin: 0 auto;
        margin-right: 8px;
        height: calc(100% - 60px);
        margin-top: 16px;
        padding: 0 16px 16px;
        position: relative;
        overflow-x: hidden;
        @include m.scrollbar;
        overflow-y: scroll;
        font-size: 14px;
        @include m.desktop {
            margin-top: 24px;
            padding: 0 16px px;
            height: calc(100% - 76px);
        }
        & > *:first-child {
            margin-top: 0;
        }
        & img {
            margin: 16px 0;
        }
        & p {
            margin-top: 16px;
        }
        & a {
            color: v.$accent;
			text-decoration-line: underline;
			text-decoration-style: dotted;
			text-underline-offset: 20%;
			text-decoration-thickness: 12%;
        }
        & span {
            color: v.$accent;
        }
        & ol {
            list-style-type: decimal;
            list-style-position: inside;
        }
        & strong {
            font-weight: 700;
            text-transform: uppercase;
            color: v.$accent;
        }
    }
    &__title {
        font-weight: 600;
        font-size: 20px;
        color: v.$text-light;
    }
}
