@use "../../commons/var";
@use "../../commons/mixins";

.about-benefit {
    margin-top: 64px;
    border-radius: 8px;
    background-color: var.$bg-secondary;
    border: 1px solid var.$border;
    padding: 23px;
    @include mixins.tablet {
        margin-top: 96px;
        padding: 47px;
    }
    @include mixins.desktop {
        margin-top: 120px;
    }
    &__title {
        font-weight: 700;
        font-size: 24px;
        @include mixins.tablet {
            font-size: 32px;
        }
    }
    &__text {
        color: var.$text-primary;
        font-size: 14px;
        margin-top: 16px;
        @include mixins.tablet {
            margin-top: 24px;
            font-size: 16px;
        }
    }
    &__list {
        margin-top: 32px;
        display: flex;
        flex-direction: column;
        gap: 24px;
        @include mixins.tablet {
            margin-top: 36px;
            flex-direction: row;
            flex-wrap: wrap;
            align-items: stretch;
        }
    }
    &__item {
        padding: 23px;
        border: 1px solid var.$border;
        border-radius: 8px;
        @include mixins.tablet {
            width: calc(50% - 12px);
            display: flex;
            flex-direction: column;
            gap: 16px;
        }
        @include mixins.desktop {
            width: calc(25% - 18px);
        }
        &-title {
            font-weight: 700;
            font-size: 20px;
        }
        &-text {
            color: var.$text-primary;
        }
        &-number {
            padding-top: 8px;
            margin-top: auto;
            color: var.$text-primary;
        }
    }
}