@use "../../commons/var" as v;
@use "../../commons/mixins" as m;

.video {
    position: relative;
    & .video__container .vjs-poster img {
        object-fit: cover;
    }
    &__container {
        max-width: 100%;
        position: relative;
        overflow: visible;
        border-radius: 11.4px 11.4px 0 0;
        border-width: 1px 1px 0;
        border-color: #74EB694D;
        border-style: solid;
        padding-bottom: 25px;
        margin-bottom: -25px;
        @include m.tablet {
            border-radius: 24px 24px 0 0;
            padding-bottom: 0;
            margin-bottom: 0;
        }
        @include m.desktop {
            border-radius: 40px 40px 0 0;
        }
        &::before {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: linear-gradient(180deg, #285A23 0%, rgba(40, 90, 35, 0) 100%);
            filter: blur(44px);
            z-index: -1;
        } 
    }
    &__wrapper {
        padding: 8px 8px 0;
        overflow: hidden;
        position: relative;
        width: 100%;           
        aspect-ratio: 19 / 10;
        border-radius: 11.4px 11.4px 0 0;
        @include m.tablet {
            border-radius: 24px 24px 0 0;
        }
        @include m.desktop {
            border-radius: 40px 40px 0 0;
            padding: 16px 16px 0;
        }
        &::before {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            background: linear-gradient(180deg, rgba(255, 255, 255, 0.1) 0%, rgba(255, 255, 255, 0) 100%);
            filter: blur(8px);
            z-index: -1;
            pointer-events: none;
        } 
        &__bg {
            position: absolute;
            pointer-events: none;
        }
        & .video-js {
            border-radius: 7.6px 7.6px 0 0;
            overflow: hidden;
            background-color: transparent;
            border-width: 0.5px 0.5px 0px .5px;
            border-style: solid;
            border-color: #FFFFFF4D;
            @include m.tablet {
                border-radius: 16px 16px 0 0;
            }
            @include m.desktop {
                border-radius: 28px 28px 0 0;
            }
        }
        & .video-js  .vjs-tech {
            height: 102%;
            width: 102%;
            left: -1%;
            top: -1%;
            background-color: #101010;
        }
        & video {
            width: 100%;
        }
    }
    &__play {
        position: absolute;
        right: 24px;
        bottom: 31px;
        content: "";
        z-index: 2;
        border: 0;
        border-radius: 50%;
        width: 40px;
        height: 40px;
        cursor: pointer;
        background-color: v.$bg-secondary;
        box-shadow: 0px 10px 50px 0px rgba(0, 0, 0, 0.1);
        @include m.tablet {
            bottom: 16px;
        }
        @include m.desktop {
            right: 48px;
            bottom: 32px;
            width: 64px;
            height: 64px;
        }
        &::before {
            content: '';
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            @include m.svg-mask("pause");
            mask-size: contain;
            mask-position: center;
            background-color: v.$text-secondary;
            mask-size: 12px;
            @include m.desktop {
                mask-size: 16px;
            }
        }
        &.paused::before {
            @include m.svg-mask("play");
            mask-size: 12px;
            @include m.desktop {
                mask-size: 16px;
            }
        }
        @include m.hover {
            &::before {
                background-color: v.$text-light;
            }
        }

    }
    &__tabs {
        display: flex;
        justify-content: center;
        &-list {
            overflow-x: auto;
            display: inline-flex;
            align-items: center;
            gap: 8px;
            padding: 7px;
            border: 1px solid v.$border;
            border-radius: 16px;
        }
        &-item {
            scroll-snap-align: start;
            padding: 11px 19px;
            border: 1px solid transparent;
            border-radius: 8px;
            text-align: center;
            font-weight: 600;
            font-size: 14px;
            color: v.$text-light;
            white-space: nowrap;
            min-width: 176px;
            cursor: pointer;
            @include m.tablet {
                min-width: 200px;
            }
            @include m.desktop {
                font-size: 16px;
            }
            @include m.hover {
                border-color: v.$border;
            }
            &.active {
                color: v.$text-dark;
                border-color: v.$accent;
                background-color: v.$accent;
                pointer-events: none;
            }
        }
    }
}
