@use "../commons/var";
@use "../commons/mixins";

@use "about/_description";
@use "about/_cards";
@use "about/_what";
@use "about/_benefit";

.about {
    padding: 40px 0 64px;
    @include  mixins.tablet {
        padding: 24px 0 96px;
    }
    @include  mixins.desktop {
        padding: 40px 0 120px;
    }
    & .blog-block {
        margin-top: 64px;
        @include  mixins.tablet {
            margin-top: 96px;
        }
        @include  mixins.desktop {
            margin-top: 120px;
        }
    }
}