@use "../../commons/var";
@use "../../commons/mixins";

.price-description {
    margin-top: 64px;
    color: var.$text-primary;
    @include mixins.tablet {
        margin-top: 96px;
    }
    @include mixins.desktop {
        margin-top: 64px;
    }
    & h2, h3, h4, h5, h6 {
        color: var.$text-light;
        font-size: 24px;
        &:not(:first-child) {
            margin-top: 40px;
        }
    } 
    & p {
        &:not(:first-child) {
            margin-top: 16px;
        }
    }
    &__links {
        margin-top: 40px;
        @include mixins.tablet {
            margin-top: 64px;
        }
    }
    &__title {
        font-weight: 700;
        font-size: 24px;
    }
    &__list {
        margin-top: 16px;
        @include mixins.flex-center;
        flex-wrap: wrap;
        gap: 8px;
    }
    &__item {
        @include mixins.flex-center;
        gap: 12px;
        border: 1px solid var.$border;
        padding: 9px 19px 9px 11px;
        border-radius: 4px;
        font-weight: 600;
        font-size: 14px;
        color: var.$text-light;
        @include mixins.hover {
            background-color: var.$bg-secondary;
            border-color: var.$bg-secondary;
        }
        &::before {
            content: '';
            width: 16px;
            height: 16px;
            background-color: var.$text-primary;
        }
        &--whitepaper::before {
            @include mixins.svg-mask('book');
        }
        &--site::before {
            @include mixins.svg-mask('web');
        }
    }
}