@use "../../commons/var" as v;
@use "../../commons/mixins" as m;

.descriptor {
    width: 100%;
    position: relative;
    padding: 96px 0 80px;
	overflow: hidden;
    @include m.tablet {
        padding: 96px 0 80px;
    }
    @include m.desktop {
        padding: 96px 0 96px;
    }
    & .container {
        position: relative;
        z-index: v.$ui-index-2;
    }
    &-inner {
        text-align: center;
    }
    &__title {
        font-weight: 700;
        font-size: 40px;
        line-height: 1.2;
        font-family: v.$roboto;
        & span {
            color: v.$accent;
        }
        @include m.desktop {
            font-size: 56px;
        }
    }
    &__subtitle {
        font-family: v.$roboto;
        margin-top: 40px;
        font-size: 20px;
        color: v.$text-primary;
        & b {
            color: v.$text-light;
        }
        @include m.tablet {
            @include m.flex-center-center;
			gap: 12px;
			margin-top: 44px;
        }
        @include m.desktop {
			margin-top: 45px;
            font-size: 32px;
            line-height: 1.4;
        }
    }
    &__icons {
        display: none;
        @include m.tablet {
            display: block;
            position: relative;
            cursor: pointer;
			width: 40px;
			height: 40px;
        }
		@include m.hover {
			& img:last-child {
				transform: translateY(-3px);
			}
		}
        &.swap {
            & img:first-child {
                animation: swapUsdtDisabled 0.3s linear forwards;
            }
            & img:last-child {
                animation: swapTrx 0.3s linear forwards;
            }
			@include m.hover {
				& img:first-child {
					transform: translateY(-3px);
				}
				& img:last-child {
					transform: translateY(0);
				}
			}
        }
        & img {
            transition-property: all;
            transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
            transition-duration: 0.15s;
            position: absolute;
			display: block;
			width: 32px;
			height: 32px;
            user-select: none;
            &:first-child {
                animation: swapUsdt 0.3s linear forwards;
            }
            &:last-child {
                animation: swapTrxDisable 0.3s linear forwards;
            }
        }
    }
    &__btns {
        margin-top: 48px;
        @include m.tablet {
            margin-top: 60px;
            @include m.flex-center-center;
			gap: 16px;
        }
        @include m.desktop {
            margin-top: 58px;
        }
    }
    &__info {
        @include m.flex-center;
        justify-content: center;
        width: 100%;
        margin-top: 16px;
        padding: 15px;
        gap: 8px;
        color: v.$text-light;
        border: 0;
        border-radius: 8px;
        background-color: transparent;
        font-weight: 600;
        border: 1px solid v.$border;
        cursor: pointer;
        &::before {
            content: "";
            width: 22px;
            height: 22px;
            mask-size: contain;
            mask-image: url(../img/icons/info.svg);
            background-color: v.$text-light;
            transition: all 0.2s linear;
        }
        @include m.hover {
            background-color: v.$border;
        }
        @include m.tablet {
            margin-top: 0;
            width: auto;
        }
        @include m.desktop {
			padding: 15px 23px 15px 15px;
        }
    }
    &__start {
        width: 100%;
        display: block;
        font-weight: 600;
        display: flex;
        justify-content: center;
        align-items: center;
        align-self: flex-start;
        gap: 16px;
        background-color: #2aabee;
        border-radius: 8px;
        padding: 16px;
        white-space: nowrap;
        @include m.hover {
            background-color: #5dbff2;
        }
        @include m.tablet {
            width: auto;
        }
		@include m.desktop {
			padding: 16px 23px 16px 15px;
		}
        &::before {
            content: "";
            width: 22px;
            height: 22px;
            mask-image: url(../img/icons/telegram.svg);
            mask-position: center;
            mask-repeat: no-repeat;
            mask-size: contain;
            background-color: v.$white;
        }
    }
}

@keyframes swapUsdt {
    0% {
        z-index: 20;
        rotate: 6deg;
        top: -8px;
    }

    50% {
        z-index: 20;
        rotate: 0deg;
        top: -2px;
    }
    100% {
        z-index: 20;
        rotate: 0deg;
        top: 0;
    }
}
@keyframes swapUsdtDisabled {
    0% {
        z-index: 10;
        rotate: 0deg;
        top: 0;
        left: 0;
    }
    50% {
        z-index: 10;
        rotate: 3deg;
        top: -4px;
        left: 4px;
    }
    100% {
        z-index: 10;
        rotate: 6deg;
        top: -8px;
        left: 8px;
    }
}
@keyframes swapTrx {
    0% {
        z-index: 10;
        rotate: 6deg;
        top: -8px;
    }

    50% {
        z-index: 10;
        rotate: -7deg;
        top: -4px;
    }
    100% {
        z-index: 10;
        rotate: -15deg;
        top: 0px;
    }
}

@keyframes swapTrxDisable {
    0% {
        z-index: 10;
        rotate: 0deg;
        top: 0;
        left: 0;
    }
    50% {
        z-index: 10;
        rotate: 3deg;
        top: -4px;
        left: 4px;
    }
    100% {
        z-index: 10;
        rotate: 6deg;
        top: -8px;
        left: 8px;
    }
}
