@use "../commons/var" as v;
@use "../commons/mixins" as m;

.calculator__range {
    &.noUi-target {
        background-color: v.$border;
        border: 0;
        box-shadow: initial;
        border-radius: 0;
        height: 1px;
        &::before {
            content: '';
            top: 0;
            left: 0;
            position: absolute;
            background-color: v.$accent;
            width: 6px;
            height: 1px;
            z-index: 3;
        }
    }
    .noUi-base {
        width: calc(100% - 12px);
        height: 12px;
        top: -6px;
        margin: 0 auto;
        background-color: transparent;
        &::after {
            content: '';
            top: 6px;
            right: -6px;
            position: absolute;
            background-color: v.$border;
            width: 6px;
            height: 1px;
        }
    }
    
    .noUi-connects {
        border-radius: 0;
        top: 6px;
    }
    .noUi-connect {
        background-color: v.$accent;
        height: 1px;
    }
    .noUi-handle {
        width: 12px;
        height: 12px;
        border-radius: 50%;
        top: -0px;
        right: -6px;
        background-color: v.$accent;
        border: 0;
        box-shadow: initial;
        &::before, &::after {
            display: none;
        }
    }
    .noUi-touch-area {
        cursor: pointer;
        width: 200%;
        height: 200%;
        transform: translate(-25%, -25%);
    }
    .noUi-pips-horizontal {
        padding: 0;
        padding-top: 6px;
    }
    .noUi-pips {
        height: auto;
    }
    .noUi-marker {
        display: none;
        height: auto;
    }
    .noUi-value {
        transform: none;
        font-size: 12px;
        color: v.$text-secondary;
        &:last-child {
            transform: translateX(-100%);
        }
    }
}