@use "../../commons/var";
@use "../../commons/mixins";

.price-info {
    margin-top: 24px;
    @include mixins.desktop {
        margin-top: 0;
    }
    &__inner {
        border-radius: 8px;
        padding: 16px;
        background-color: var.$bg-secondary;
        @include mixins.tablet {
            padding: 24px;
        }
    }
    &__blocks {
        margin-top: 24px;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 16px;
        @include mixins.tablet {
            grid-template-columns: repeat(4, 146px);
        }
        @include mixins.desktop {
            grid-template-columns: 1fr 1fr;
        }
    }
    &__block {
        position: relative;
    }
    &__label {
        color: var.$text-primary;
        font-size: 14px;
        &--hint {
            cursor: help;
            text-decoration: underline;
            text-decoration-style: dotted;
            text-decoration-thickness: 10%;
            &.active + .price-info__tooltip {
                display: block;
            }
        }
    }
    &__value {
        font-weight: 700;
        margin-top: 2px;
    }
    &__tooltip {
        position: absolute;
        display: none;
        width: 308px;
        bottom: calc(100% + 16px);
        left: 0;
        padding: 8px 12px;
        border-radius: 12px;
        background-color: var.$gray;
        font-size: 14px;
        @include mixins.tablet {
            left: 24px;
        }
        @include mixins.desktop {
            left: calc(50% - 174px);
        }
        &::before {
            position: absolute;
            content: '';
            width: 24px;
            height: 12px;
            bottom: -11px;
            left: 36px;
            mask: url(../img/icons/beak.svg) center no-repeat;
            transform: rotate(180deg);
            mask-size: contain;
            background-color: var.$gray;
            @include mixins.tablet {
                left: 12px;
            }
            @include mixins.desktop {
                left: calc(50% - 12px);
            }
        }
    }
}
