@use "../commons/var";
@use "../commons/mixins";

.steps {
    margin: 64px 0;
    padding: 64px 0 72px;
    background-color: var.$bg-secondary;
    @include mixins.tablet {
        margin: 64px 0 96px;
    }
    @include mixins.desktop {
        margin: 64px 0 120px;
    }
    &-inner {
        text-align: center;
        @include mixins.desktop {
            text-align: left;
        }
    }
    &__title {
        font-weight: 700;
        font-size: 24px;
        @include mixins.tablet {
            font-size: 40px;
        }
    }
    &__description {
        margin-top: 16px;
        color: var.$text-primary;
        @include mixins.tablet {
            font-size: 20px;
        }
    }
    &__list {
        margin-top: 56px;
        display: flex;
        flex-direction: column;
        gap: 32px;
        @include mixins.tablet {
            flex-direction: row;
            flex-wrap: wrap;
            justify-content: center;
            gap: 32px 80px;
        }
        @include mixins.desktop {
            flex-wrap: nowrap;
            gap: 120px;
            text-align: center;
        }
    }
    &__item {
        @include mixins.tablet {
            position: relative;
            width: calc(50% - 40px);
            &:first-child::after {
                content: '';
                position: absolute;
                width: 72px;
                height: 1px;
                background-color: var.$border;
                top: 50%;
                right: -84px;
            }
        }
        @include mixins.desktop {
            width: calc(33.3333% - 80px);
            &:not(:last-child)::after {
                content: '';
                position: absolute;
                width: 72px;
                height: 1px;
                background-color: var.$border;
                top: 50%;
                right: -84px;
            }
        }
    }
    &__icon {
        display: inline-block;
        width: 48px;
        & img {
            width: 100%;
        }
    }
    &__name {
        margin-top: 24px;
        font-weight: 700;
        font-size: 20px;
    }
    &__text {
        margin-top: 16px;
        color: var.$text-primary;
    }
}