@use "../commons/var";
@use "../commons/mixins";

.scroll-btn {
    display: none;
    @include mixins.desktop {
        position: fixed;
        right: 40px;
        bottom: 40px;
        padding: 11px 19px 11px 11px;
        border: 1px solid var.$accent;
        background-color: var.$accent;
        @include mixins.flex-center;
        gap: 12px;
        cursor: pointer;
        color: var.$text-dark;
        font-weight: 500;
        font-size: 14px;
        border-radius: 8px;
        opacity: 0;
        transition: opacity 0.3s ease-in-out;
    }
    &::before {
        content: "";
        width: 20px;
        height: 20px;
        mask: url(../img/icons/arrow.svg) center no-repeat;
        background-color: var.$text-dark;
    }
    &.show {
        opacity: 1;
        visibility: visible;
        z-index: var.$ui-index-3;
    }

    &.hide {
        opacity: 0;
        visibility: hidden;
        pointer-events: none;
        z-index: -1;
    }
}
