@use "../commons/var";
@use "../commons/mixins";

.socials {
    display: flex;
    align-items: center;
    gap: 16px;
}

.social {
    display: block;
    width: 40px;
    height: 40px;
    mask-position: center;
    mask-repeat: no-repeat;
    mask-size: cover;
    background-color: var.$text-secondary;
    @include mixins.hover {
        background-color: var.$white;
    }
    &--x {
        mask-image: url(../img/icons/xtwitter.svg);
    }
    &--telegram {
        mask-image: url(../img/icons/telegram.svg);
    }
    &--youtube {
        mask-image: url(../img/icons/youtube.svg);
    }
    &--facebook {
        mask-image: url(../img/icons/facebook.svg);
    }
    &--reddit {
        mask-image: url(../img/icons/reddit.svg);
    }
    &--vk {
        mask-image: url(../img/icons/vk.svg);
    }
}