@use "../../commons/var";
@use "../../commons/mixins";

.price-survey {
    margin-top: 24px;
    background-color: var.$bg-secondary;
    border-radius: 8px;
    &__inner {
        padding: 16px;
        @include mixins.tablet {
            padding: 24px;
        }
    }
    &__lines {
        margin-top: 24px;
        display: flex;
        align-items: center;
        gap: 4px;
        &.hide {
            display: none;
        }
    }
    &__line {
        width: calc(50% - 2px);
        height: 1px;
        &--green {
            background-color: var.$accent;
        }
        &--red {
            background-color: var.$red;
        }
    }
    &__btns {
        margin-top: 24px;
        display: flex;
        align-items: center;
        gap: 16px;
    }
    &__btn {
        width: calc(50% - 8px);
        & input {
            position: absolute;
            z-index: -1;
            &:checked + label {
                background-color: var.$accent;
                color: var.$text-dark;
                & span {
                    color: var.$text-dark;
                }
                &::before {
                    background-color: var.$text-dark;
                }
            }
        }
        & label {
            @include mixins.flex-center-center;
            gap: 8px;
            padding: 10px;
            border: 0;
            outline: 0;
            font-weight: 700;
            font-size: 14px;
            background-color: var.$bg;
            border-radius: 4px;
            color: var.$text-light;
            cursor: pointer;
            &::before {
                content: '';
                width: 20px;
                height: 20px;
                @include mixins.svg-mask('like');
                background-color: var.$text-primary;
            }
            & span {
                color: var.$accent;
            }
            @include mixins.hover {
                background-color: var.$hover-additional;
            }
        }
        &--dislike {
            & label::before {
                transform: scale(1,-1);
            }
            & label span {
                color: var.$red;
            }
            & input:checked + label {
                color: var.$text-light;
                background-color: var.$red;
                &::before {
                    background-color: var.$text-light;
                }
                & span {
                    color: var.$text-light;
                }
            }
        }
    }
    &__hint {
        margin-top: 16px;
        font-size: 12px;
        color: var.$text-secondary;
        @include mixins.flex-center;
        gap: 6px;
        &::before {
            content: '';
            width: 17px;
            height: 17px;
            @include mixins.svg-mask('info');
            background-color: var.$text-secondary;
        }
    }
}