@use "../../commons/var";
@use "../../commons/mixins";

.faq {
    padding: 64px 0;
    @include mixins.tablet {
        padding: 96px 0;
    }
    @include mixins.desktop {
        padding: 120px 0;
    }
    &--bg {
        padding: 64px 0;
        margin-top: 64px;
        border-radius: 32px 32px 0 0;
        background: linear-gradient(180deg, #1F1F1F 0%, rgba(31, 31, 31, 0) 100%);
        border-top: 1px solid var.$border;
        @include mixins.tablet {
            margin-top: 96px;
        }
        @include mixins.desktop {
            padding: 96px 0;
        }
        & .faq__title {
            font-family: var.$roboto;
            font-size: 32px;
            @include mixins.tablet {
                font-size: 40px;
            }
        }
        & .faq__answer {
            height: initial;
            max-height: initial;
            padding-top: 12px;
            @include mixins.tablet {
                padding-top: 22px;
            }
        }
    }
    &__header {
        @include mixins.desktop {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            align-items: center;
        }
    }
    &__title {
        font-size: 32px;
        font-weight: 700;
        @include mixins.tablet {
            font-size: 40px;
        }
        @include mixins.desktop {
            order: 1px;
        }
    }
    &__subtitle {
        color: var.$text-primary;
        margin-top: 16px;
        @include mixins.tablet {
            font-size: 20px;
        }
        @include mixins.desktop {
            order: 3;
        }
    }
    &__link {
        margin-top: 32px;
        padding: 10px 12px;
        display: inline-flex;
        align-items: center;
        gap: 8px;
        color: var.$text-dark;
        font-weight: 600;
        background-color: var.$accent-secondary;
        border-radius: 4px;
        @include mixins.hover {
            background-color: var.$text-secondary;
        }
        &::after {
            margin-top: 1px;
            content: '';
            width: 16px;
            height: 16px;
            mask-image: url(../img/icons/arrow-thin.svg);
            mask-size: contain;
            background-color: var.$text-dark;
        }
        @include mixins.desktop {
            margin-top: 0;
            order: 2;
        }
    }
    &__list {
        margin-top: 32px;
        list-style-type: none;
    }
    &__item {
        border-bottom: 1px solid var.$border;
        gap: 8px;
        padding-bottom: 20px;
        counter-increment: faq-counter; 
        overflow: hidden;
        &:not(:first-child) {
            padding-top: 20px;
        }
        &:last-child {
            border-bottom: 0;
            padding-bottom: 0;
        }
        &.active .faq__accordion-animation {
            grid-template-rows: 1fr;
        }
        &.active .faq__accordion-animation .faq__accordion-content {
            transform: translateY(0);
            visibility: visible;
            transition: transform 0.4s ease,
                visibility 0s linear;
        }
        &.active .faq__question::after {
            transform: rotate(45deg);
        }
    }
    &__text {
        width: calc(100% - 40px);
    }
    &__question {
        display: flex;
        justify-content: space-between;
        align-items: center;
        flex-grow: 1;
        gap: 8px;
        font-size: 20px;
        font-weight: 700;
        position: relative;
        cursor: pointer;
        &::before {
            content: counter(faq-counter) ".";
            min-width: 32px;
            font-weight: 700;
            font-size: 20px;
            top: calc(50% - 24px);
            color: var.$text-secondary;
        }
    }
    &__toggle {
        flex: 0 0 48px;
        width: 48px;
        height: 48px;
        border-radius: 4px;
        border: 0;
        background-color: transparent;
        position: relative;
        cursor: pointer;
        @include mixins.hover {
            background-color: var.$bg-secondary;
            &::after {
                background-color: var.$accent;
            }
        }
        .open &, .active & {
            &::after {
                transform: rotate(45deg);
            }
            @include mixins.hover {
                &::after {
                    background-color: var.$red;
                }
            }
        }
        &::after {
            position: absolute;
            content: '';
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            mask-image: url(../img/icons/plus.svg);
            mask-position: center;
            mask-repeat: no-repeat;
            background-color: var.$text-light;
            transition: all .2s ease-in-out;
        }
    }
    &__answer {
        height: 0;
        max-height: 0;
        overflow: hidden;
        color: var.$text-primary;
        padding-left: 40px;
        & a {
            color: var.$text-light;
            text-decoration: underline;
        }
        .open & {
            max-height: 500px;
            height: auto;
            margin-top: 12px;
            @include mixins.tablet {
                margin-top: 24px;
            }
        }
    }
    &__accordion-animation {
        display: grid;
        grid-template-rows: 0fr;
        overflow: hidden;
        transition: grid-template-rows 0.4s ease;
    }
    &__accordion-wrapper {
        min-height: 0;
    }
    &__accordion-content {
        transform: translateY(-100%);
        visibility: hidden;
        transition: transform  0.4s ease,
            visibility 0s  0.4s ease;
    }
}