@use "../commons/var";
@use "../commons/mixins";

.breadcrumbs {
    @include mixins.flex-center;
    flex-wrap: wrap;
    font-size: 14px;
    color: var.$text-secondary;
    row-gap: 8px;
    &.center {
        justify-content: center;
    }
    &:not(:first-child) {
        margin-top: 24px;
    }
}
.breadcrumb {
    &:not(:last-child) a {
        position: relative;
        margin-right: 23px;
        color: var.$text-secondary;
        &::after {
            position: absolute;
            content: '/';
            color: var.$text-secondary;
            font-size: 14px;
            top: 50%;
            right: -14px;
            transform: translateY(-50%);
        }
    }
    & a {
        @include mixins.hover {
            color: var.$text-light;
        }
    }
}