@use "../commons/var";
@use "../commons/mixins";

.api {
    padding: 48px 0 32px;
    @include mixins.tablet {
        padding: 48px 0 0;
    }
    &-header {
        text-align: center;
    }
    &__title {
        margin-top: 4px;
        font-weight: 700;
        font-size: 28px;
        @include mixins.tablet {
            font-size: 40px;
            margin-top: 0;
        }
    }
    &__subtitle {
        margin-top: 16px;
        font-weight: 400;
        font-size: 16px;
        color: var.$text-primary;
        @include mixins.tablet {
            font-size: 20px;
        }
    }
    &-content {
        max-width: 900px;
        margin: 40px auto 0;
        border: 1px solid var.$border;
        border-radius: 8px;
        padding: 15px;
        @include mixins.tablet {
            padding: 23px;
        }
        &__header {
            padding-bottom: 23px;
            border-bottom: 1px solid var.$border;
        }
        &__title {
            font-weight: 700;
            font-size: 20px;
        }
        &__text {
            margin-top: 16px;
            color: var.$text-primary;
            font-size: 14px;
            & a {
                font-weight: 700;
                color: var.$accent;
                @include mixins.hover {
                    color: var.$hover-primary;
                }
            }
        }
        &__link {
            margin-top: 8px;
            @include mixins.flex-center;
            gap: 4px;
            font-weight: 500;
            font-size: 14px;
            color: var.$accent;
            @include mixins.hover {
                color: var.$hover-primary;
                &::after {
                    background-color: var.$hover-primary;
                }
            }
            &::after {
                content: "";
                width: 16px;
                height: 16px;
                @include mixins.svg-mask("arrow");
                background-color: var.$accent;
                transform: rotate(90deg);
            }
            &--center {
                margin-top: 32px;
                justify-content: center;
                font-size: 16px;
                font-weight: 600;
                &::after {
                    @include mixins.svg-mask("arrow-thin");
                    transform: none;
                }
            }
        }
        &__inner {
            margin-top: 24px;
        }
        &__block {
            margin-top: 24px;
            &:not(:first-of-type) {
                margin-top: 48px;
            }
        }
        &__box {
            @include mixins.flex-center;
            gap: 16px;
        }
        &__label {
            padding: 4px 8px;
            background-color: var.$accent;
            border-radius: 4px;
            color: var.$text-dark;
            font-weight: 600;
        }
        &__address {
            font-weight: 600;
            color: #abb2bf;
        }
        &__description {
            margin-top: 16px;
            color: var.$text-primary;
            font-size: 14px;
        }
        &__code {
            margin-top: 24px;
        }
        &__name {
            font-weight: 700;
            font-size: 14px;
        }
        &__toggle {
            margin-left: auto;
            border: 0;
            background-color: transparent;
            outline: 0;
            padding: 0;
            @include mixins.flex-center;
            gap: 4px;
            color: var.$text-secondary;
            font-size: 14px;
            line-height: 1.4;
            cursor: pointer;
            @include mixins.hover {
                color: var.$text-light;
                &::after {
                    background-color: var.$text-light;
                }
            }
            &::after {
                content: "";
                width: 16px;
                height: 16px;
                @include mixins.svg-mask("chevron");
                background-color: var.$text-secondary;
                transition: transform .1s linear;
            }
            &.active::after {
                transform: rotate(180deg);
            }
        }
    }
    &-code {
        &__wrap {
            margin-top: 16px;
            background-color: #23272e;
            border-radius: 4px;
            padding: 16px;
            font-family: var.$fira;
            &.hide {
                display: none;
            }
            &.show {
                display: block;
            }
        }
        &__head {
            @include mixins.flex-center-space;
        }
        &__label {
            font-size: 14px;
            color: var.$text-secondary;
        }
        &__copy {
            border: 0;
            background-color: transparent;
            outline: 0;
            padding: 0;
            @include mixins.flex-center;
            gap: 4px;
            color: var.$text-secondary;
            font-size: 14px;
            line-height: 1.4;
            cursor: pointer;
            @include mixins.hover {
                color: var.$text-light;
                &::before {
                    background-color: var.$text-light;
                }
            }
            &::before {
                content: "";
                width: 16px;
                height: 16px;
                @include mixins.svg-mask("copy-liner");
                background-color: var.$text-secondary;
                pointer-events: none;
                transition: background-color .1s ease-out;
            }
            &.copied {
                color: var.$accent;
                pointer-events: none;
                &::before {
                    mask-image: url(../img/icons/check.svg);
                    background-color: var.$accent;
                }
            }
        }
        &__text {
            margin-top: 8px;
            & code {
                white-space: pre-wrap;
                word-break: break-all;
                font-family: var.$fira;
                font-size: 14px;
                color: var.$text-primary;
                & .red {
                    color: var.$red;
                }
                & .yellow {
                    color: var.$yellow;
                }
                & .green {
                    color: var.$accent;
                }
                & .purple {
                    color: var.$purple;
                }
            }
        }
    }
}
