@use "../../commons/var";
@use "../../commons/mixins";

.price-banner {
    margin-top: 24px;
    background-image: url(../img/banner-buy-energy-mob.png);
    background-size: cover;
    border-radius: 8px;
    @include mixins.tablet {
        background-image: url(../img/banner-buy-energy.png);
    }
    @include mixins.desktop {
        background-image: url(../img/banner-buy-energy-mob.png);
    }
    &__inner {
        padding: 40px 38px 40px 40px;
        @include mixins.tablet {
            display: grid;
            grid-template-columns: 1fr 1fr;
        }
        @include mixins.desktop {
            display: block;
        }
    }
    &__logo {
        font-weight: 700;
        @include mixins.flex-center;
        gap: 8px;
        margin-left: -4px;
        &::before {
            content: '';
            width: 26px;
            height: 26px;
            background-image: url(../img/icons/logo-transparent.svg);
            background-size: contain;
            background-position: center;
            background-repeat: no-repeat;
        }
        @include mixins.tablet {
            align-items: initial;
        }
    }
    &__title {
        margin-top: 24px;
        font-weight: 800;
        font-size: 24px;
        color: var.$accent;
        @include mixins.tablet {
            grid-column: 1;
            margin-top: 6px;
        }
        @include mixins.desktop {
            margin-top: 24px;
        }
    }
    &__subtitle {
        font-size: 20px;
        @include mixins.tablet {
            grid-column: 1;
        }
    }
    &__text {
        margin-top: 16px;
        color: var.$text-primary;
        max-width: 220px;
        @include mixins.tablet {
            grid-row: 1;
            grid-column: 2;
            text-align: right;
            margin-top: 0;
            margin-left: auto;
        }
        @include mixins.desktop {
            text-align: left;
            margin-top: 16px;
            margin-left: 0;
        }
    }
    &__link {
        display: inline-flex;
        align-items: center;
        gap: 8px;
        margin-top: 24px;
        color: var.$text-dark;
        font-size: 14px;
        font-weight: 600;
        padding: 6px 12px 6px 8px;
        background-color: var.$accent;
        border-radius: 4px;
        @include mixins.hover {
            background-color: var.$hover-primary;
        }
        &::before {
            content: '';
            width: 16px;
            height: 16px;
            @include mixins.svg-mask('energy');
            background-color: var.$text-dark;
        }
        @include mixins.tablet {
            margin-top: 0;
            margin-left: auto;
            grid-column: 2;
            grid-row: 2/4;
            align-self: flex-end;
        }
        @include mixins.desktop {
            margin-left: 0;
            margin-top: 24px;
        }
    }
}