@use "../commons/var";
@use "../commons/mixins";

.footer {
    border-top: 1px solid var.$border;
    padding: 40px 0 32px;
    position: relative;
    z-index: var.$ui-index-2;
    @include mixins.desktop {
        padding: 40px 0 42px;
    }
    &-inner {
        @include mixins.tablet {
            display: grid;
            grid-template-columns: 220px 1fr;
            gap: 0 76px;
        }
        @include mixins.desktop {
            gap: 0 128px;
        }
    }
    &-head {
        @include mixins.flex-center-space;
        padding-bottom: 31px;
        border-bottom: 1px solid var.$border;
        @include mixins.tablet {
            padding: 0;
            border: 0;
            flex-direction: column;
            align-items: flex-start;
        }
    }
    &__logo {
        width: 116px;
        & img {
            width: 100%;
        }
    }
    &__socials {
        gap: 8px;
        @include mixins.tablet {
            display: none;
        }
        &--tablet {
            display: none;
            @include mixins.tablet {
                display: flex;
                justify-content: flex-end;
                order: 3;
            }
            @include mixins.desktop {
                justify-content: flex-start;
            }
        }
        & .social {
            width: 32px;
            height: 32px;
            @include mixins.tablet {
                width: 24px;
                height: 24px;
            }
        }
    }
    &__links {
        @include mixins.tablet {
            display: flex;
            flex-wrap: wrap;
            gap: 36px 24px;
        }
        @include mixins.desktop {
            flex-wrap: nowrap;
            gap: 24px;
        }
        &-box {
            border-bottom: 1px solid var.$border;
            padding: 24px 0;
            @include mixins.tablet {
                padding: 0;
                border: 0;
                width: calc(50% - 12px);
            }
            @include mixins.desktop {
                width: calc(25% - 18px);
            }
        }
        &-toggle {
            font-weight: 700;
            font-size: 16px;
            @include mixins.flex-center-space;
            gap: 16px;
            @include mixins.tablet {
                text-transform: uppercase;
                font-size: 10px;
                color: var.$text-secondary;
            }
            &.active::after {
                transform: scaleY(-1);
            }
            &::after {
                content: '';
                width: 20px;
                height: 20px;
                mask: url(../img/icons/chevron.svg) center no-repeat;
                background-color: var.$text-primary;
                transition: transform .1s ease-out;
                @include mixins.tablet {
                    display: none;
                }
            }
        }
        &-list {
            margin-top: 16px;
            display: none;
            @include mixins.tablet {
                display: block;
            }
            &.show {
                display: block;
            }

        }
    }
    &__link {
        &:not(:last-child) {
            margin-bottom: 12px;
            @include mixins.tablet {
                margin-bottom: 8px;
            }
        }
        & a {
            font-size: 14px;
            color: var.$text-primary;
            @include mixins.hover {
                color: var.$text-light
            }
            @include mixins.tablet {
                color: var.$text-light;
                @include mixins.hover {
                    color: var.$text-primary
                }
            }
        }
    }
    &-info {
        margin-top: 32px;
        text-align: center;
        display: flex;
        flex-direction: column;
        @include mixins.tablet {
            padding-top: 24px;
            margin-top: 24px;
            display: grid;
            grid-template-columns: 1fr auto 1fr;
            align-items: center;
            grid-column: 1/3;
            border-top: 1px solid var.$border;
        }
        @include mixins.desktop {
            padding-top: 42px;
            margin-top: 40px;
            gap: 24px;
            grid-template-columns: auto 195px 195px 195px 195px;
        }
    }
    &__copyright {
        text-align: center;
        color: var.$text-secondary;
        font-size: 14px;
        order: 3;
        margin-top: 24px;
        @include mixins.tablet {
            text-align: left;
            font-size: 12px;
            margin-top: 0;
            & br {
                display: none;
            }
        }
        @include mixins.desktop {
            grid-column: 2/4;
        }
    }
    &__tron {
        margin-top: 32px;
        @include mixins.flex-center-center;
        gap: 6px;
        color: var.$text-secondary;
        font-size: 14px;
        order: 4;
        @include mixins.tablet {
            margin-top: 0;
            order: 1;
            justify-content: flex-start;
        }
    }
    &__trustpilot {
        order: 2;
        margin-top: 24px;
        color: var.$text-primary;
        font-size: 14px;
        @include mixins.flex-center-center;
        gap: 8px;
        @include mixins.tablet {
            display: none;
            margin-top: auto;
            justify-content: flex-start;
        }
        @include mixins.desktop {
            display: flex;
            order: 4;
        }
        @include mixins.hover {
            color: var.$text-light;
        }
        &--tablet {
            display: none;
            @include mixins.tablet {
                display: flex;
            }
            @include mixins.desktop {
                display: none;
            }
        }
    }
    &__details {
        font-size: 14px;
        @include mixins.flex-column-center-center;
        @include mixins.tablet {
            display: none;
        }
        & span {
            color: var.$text-secondary;
        }
        & a {
            color: var.$text-secondary;
            @include mixins.hover {
                color: var.$text-light;
            }
        }
        &--tablet {
            display: none;
            @include mixins.tablet {
                margin-top: 16px;
                display: flex;
                align-items: flex-start;
            }
        }
    }
}
