@use "../commons/var";
@use "../commons/mixins";

@keyframes slideDown {
    0% {
        transform: translateY(-100%);
        opacity: 0;
    }
    100% {
        transform: translateY(0);
        opacity: 1;
    }
}
@keyframes slideUp {
    0% {
        transform: translateY(0);
        opacity: 1;
    }
    100% {
        transform: translateY(-100%);
        opacity: 0;
    }
}
.notifications {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    z-index: var.$ui-index-10;
    padding: 24px;
    display: block;
}
.notification {
    max-width: 342px;
    margin: 0 auto;
    border-radius: 8px;
    padding: 11px 47px 11px 51px;
    background-repeat: no-repeat;
    background-position: 16px 21px;     
    background-size: 20px;
    box-shadow: 0px 10px 10px 0px #00000038, 0px 14px 28px 0px #00000040;
    border: 1px solid #74EB694D;
    animation: slideDown .250s ease-in-out;
    position: relative;
    &.green {
        background-image: url('../img/icons/info-fill.svg'); 
        border: 1px solid #74EB694D;  
        background-color: #172316;
    }
    &.yellow {
        background-image: url('../img/icons/info-yellow.svg');   
        border: 1px solid #FFAF1A4D;
        background-color: #251C0E;
    }
    &.red {
        background-image: url('../img/icons/info-red.svg');  
        border: 1px solid #FF43434D; 
        background-color: #251212;
    }
    &.up {
        animation: slideUp .250s ease-in-out;
    }
    &:not(:first-child) {
        margin-top: 16px;
    }
    & h5 {
        font-weight: 700;
        font-size: 14px;
    }
    & p {
        font-size: 12px;
        margin-top: 3px;
        color: var.$text-primary;
    }
    &-close {
        position: absolute;
        top: 20px;
        right: 16px;
        cursor: pointer;
        width: 20px;
        height: 20px;
        mask-image: url('../img/icons/close.svg'); 
        mask-size: contain;
        background-color: var.$text-primary;
        z-index: var.$ui-index-10;
    }
}