@use "../../commons/var" as v;
@use "../../commons/mixins" as m;

.calculator {
    width: 100%;
    padding-bottom: 64px;
    @include m.desktop {
        padding-bottom: 96px;
    }
    &-wrapper {
        @include m.tablet {
            display: grid;
            grid-template-columns: 1fr 1fr;
            flex-wrap: wrap;
            gap: 24px;
        }
        @include m.desktop {
            display: grid;
            gap: 44px;
            grid-template-columns: 300px 524px 288px;
        }
    }
    &-block {
        margin-top: 24px;
        width: 100%;
        @include m.tablet {
            max-width: 348px;
            margin: 0 0 0 auto;
        }
        @include m.desktop {
            grid-column: 1/2;
            max-width: initial;
        }
    }
    &__form {
        margin-top: 24px;
        padding: 23px;
        border-radius: 24px;
        background-color: v.$bg-secondary;
        border: 1px solid v.$border;
        box-shadow: 0px 10px 50px 0px #0000001A;
        @include m.tablet {
            margin-top: 0;
        }
    }
    &__head {
        @include m.flex-center-space;
        gap: 16px;
    }
    &__name {
        font-weight: 700;
        font-size: 14px;
        color: v.$text-light;
        & span {
            color: v.$red;
        }
    }
    &__input {
        & input {
            text-align: right;
            padding: 6px 8px;
            background-color: v.$bg;
            border: 0;
            border-radius: 8px;
            font-size: 20px;
            line-height: 1.4;
            font-weight: 700;
            color: v.$text-light;
            width: 72px;
        }
    }
    &__range {
        margin-top: 24px;
    }
    &__checkbox {
        margin-top: 43px;
        & input {
            position: absolute;
            opacity: 0;
            z-index: -1;
            -moz-appearance: textfield;
            &:checked + label::before {
                background-image: url(../img/icons/check.svg);
                background-position: center;
                background-size: 14px;
                background-repeat: no-repeat;
                border: 1px solid v.$border;
            }
            &::-webkit-outer-spin-button,
            &::-webkit-inner-spin-button {
                -webkit-appearance: none;
                margin: 0;
            }
        }
        & label {
            @include m.flex-center;
            gap: 8px;
            cursor: pointer;
            @include m.desktop {
                font-size: 14px;
            }
            @include m.hover {
                &::before {
                    border: 1px solid v.$text-light;
                }
            }
            &::before {
                content: '';
                width: 16px;
                height: 16px;
                border-radius: 4px;
                border: 1px solid v.$border;
                background-color: transparent;
            }
        }
    }
    &__text {
        margin-top: 24px;
        font-weight: 700;
        font-size: 14px;
    }
    &__result {
        margin-top: 4px;
        & b {
            color: v.$accent;
            font-size: 24px;
        }
        & span {
            padding-left: 8px;
            color: v.$text-secondary;
            font-size: 14px;
        }
        & strike {
            display: block;
            margin-top: 3px;
            color: v.$text-secondary;
            font-size: 14px;
        }
    }
}
