.grid {
    position: relative;
    overflow: hidden;
    &::before {
        content: "";
        position: absolute;
        width: 980px;
        height: 560px;
        top: 0;
        left: calc(50vw - 490px);
        background: url(../img/index/grid.svg) center no-repeat;
        z-index: v.$ui-index-1;
    }
}