@use "../../commons/var";
@use "../../commons/mixins";

.blog-block {
    background-color: var.$bg-secondary;
    padding: 64px 0 140px; 
    position: relative;
    overflow: hidden;
    @include mixins.tablet {
        padding: 64px 0 72px;
    }
    &__header {
        display: flex;
        justify-content: space-between;
        @include mixins.tablet {
            justify-content: initial;
            gap: 16px;
        }
    }
    &__title {
        font-size: 32px;
        font-weight: 700;
        @include mixins.tablet {
            font-size: 40px;
        }
    }
    &__link {
        @include mixins.flex-center;
        gap: 6px;
        font-weight: 600;
        color: var.$accent;
        align-self: flex-end;
        padding-bottom: 3px;
        @include mixins.tablet {
            padding-bottom: 8px;
        }
        @include mixins.hover {
            color: var.$hover-primary;
            &::after {
                background-color: var.$hover-primary;
            }
        }
        &::after {
            content: '';
            width: 16px;
            height: 16px;
            mask-image: url(../img/icons/link.svg);
            background-color: var.$accent;
        }
    }
    &__arrows {
        position: absolute;
        bottom: 72px;
        left: 50%;
        transform: translateX(-50%);
        display: flex;
        flex-direction: row-reverse;
        @include mixins.tablet {
            position: initial;
            margin-left: auto;
            transform: initial;
            align-self: center;
            width: 88px;
        }
        & .swiper-button-prev, & .swiper-button-next {
            margin-top: 0;
            left: 0;
            right: 0;
            position: relative;
            width: 44px;
            height: 44px;
            transform: rotate(-90deg);
            border-radius: 4px;
            @include mixins.hover {
                background-color: var.$bg;
                &::before {
                    background-color: var.$accent;
                }
            }
            &::after {
                display: none;
            }
            &::before {
                content: '';
                position: absolute;
                width: 100%;
                height: 100%;
                top: 0;
                left: 0;
                mask-image: url(../img/icons/chevron.svg);
                mask-position: center;
                mask-repeat: no-repeat;
                mask-size: 24px;
                background-color: var.$text-light;
            }
        }
        & .swiper-button-prev {
            transform: rotate(90deg);
        }
    }
    &__slider {
        overflow: visible;
        @include mixins.desktop {
            overflow: hidden;
        }
    }
    &__items {
        width: 100%;
        margin-top: 32px;
        position: relative;
        & .blog__article {
            border: 0!important;
            padding: 0;
            @include mixins.hover {
                border: 0;
            }
            @include mixins.desktop {
                display: flex;
                align-items: center;
                gap: 16px;
            }
        }
        & .blog__article-img {
            overflow: hidden;
            border-radius: 8px;
            @include mixins.desktop {
                flex: 0 0 282px;
                margin-bottom: 0;
            }
        }
        & .blog__article-inner {
            padding: 0;
        }
        & .blog__article-title {
            margin-top: 12px;
            font-size: 16px;
        }
        & .blog__article-text {
            margin-top: 8px;
            -webkit-line-clamp: 2;
            @include mixins.desktop {
                font-size: 14px;
            }
        }
    }
}