@use "../../commons/var" as v;
@use "../../commons/mixins" as m;

.stats {
	margin-top: 24px;
	@include m.tablet {
		display: flex;
		flex-direction: column;
		max-width: 348px;
		margin: 0 auto 0 0;
	}
	@include m.desktop {
		width: 100%;
		grid-column: 3/4;
		grid-row: 1;
	}
	&__title {
		font-size: 20px;
		font-weight: 700;
	}
    &__info {
		margin-top: 16px;
    }
	&__block {
		display: flex;
		gap: 8px;
		&:not(:first-child) {
			margin-top: 16px;
		}
	}
	&__icon {
		flex: 0 0 20px;
		margin-top: 4px;
	}
	&__text {
		& span {
			color: v.$text-secondary;
			font-size: 14px;
		}
		& b {
			display: block;
			margin-top: 2px;
			font-size: 24px;
			&.small {
				color: v.$text-secondary;
				font-size: 16px;
			}
		}
	}
	&__description {
		font-weight: 700;
		& span {
			font-weight: 700;
			text-decoration: underline;
			color: v.$text-light;
		}
	}
	&__hint {
		margin-top: 16px;
		padding: 16px;
		border-radius: 8px;
		border: 1px solid v.$border;
		background-color: v.$bg-secondary;
		color: v.$text-primary;
		font-size: 14px;
		& span {
			color: v.$text-light;
			font-weight: 700;
		}
		& button {
			margin-top: 8px;
			font-weight: 600;
			color: v.$accent;
			padding: 0;
			border: 0;
			background-color: transparent;
			text-decoration-line: underline;
			text-decoration-style: dotted;
			text-underline-offset: 20%;
			text-decoration-thickness: 12%;
			cursor: pointer;
			@include m.hover {
				color: v.$hover-primary;
			}
		}
	}
	&__wallets {
		margin-top: 24px;
		border: 1px solid v.$border;
		box-shadow: 0px 10px 50px 0px #0000001A;
		background-color: v.$bg-secondary;
		border-radius: 16px;
		padding: 15px;
		display: inline-block;
		&-list {
			@include m.flex-center;
		}
		&-item {
			@include m.hover {
				& img {
					transform: translateY(-8px);
				}
			}
		}
		&-item:not(:first-child) {
			margin-left: -8px;
		}
		&-text {
			margin-top: 8px;
			font-weight: 700;
			font-size: 14px;
		}
	}
}