@use "../../commons/var";
@use "../../commons/mixins";

.about-cards {
    margin-top: 64px;
    &__list {
        @include mixins.flex-column;
        gap: 16px;
        @include mixins.tablet {
            flex-direction: row;
            flex-wrap: wrap;
            gap: 24px;
        }    
    }
    &__item {
        background-color: var.$bg-secondary;
        box-shadow: 0px -1px 0px 0px var.$accent;
        border-radius: 24px;
        padding: 24px;
        @media (hover: hover) {
            &:hover {
                box-shadow: initial;
                transform: translateY(-8px);
            }
        }
        @include mixins.tablet {
            width: calc(50% - 12px);
            padding: 32px;
            border-radius: 8px;
        }    
        @include mixins.desktop {
            width: calc(25% - 18px);
            transition: all .3s ease-in-out;
        }    
    }
    &__icon {
        width: 40px;
        height: 40px;
        & img {
            width: 100%;
        }
    }
    &__title {
        margin-top: 24px;
        font-weight: 700;
        font-size: 20px;
        @include mixins.tablet {
            margin-top: 32px;
        }
    }
    &__text {
        margin-top: 16px;
        color: var.$text-primary;
    }
}