@use "../commons/var";
@use "../commons/mixins";

.why {
    padding-bottom: 64px;
	@include mixins.tablet {
        padding-bottom: 96px;
	}
	@include mixins.desktop {
        padding-bottom: 120px;
	}
    &-inner {
        @include mixins.desktop {
            display: flex;
            gap: 48px;
        }
    }
    &__description {
        @include mixins.desktop {
            flex: 0 0 430px;
        }
    }
    &__title {
        font-weight: 700;
        font-size: 32px;
        @include mixins.tablet {
            font-size: 40px;
        }
    }
    &__text {
        margin-top: 16px;
        color: var.$text-primary;
        @include mixins.tablet {
            font-size: 20px;
        }
    }
    &__link {
        margin-top: 32px;
        padding: 10px 12px;
        display: inline-flex;
        align-items: center;
        gap: 8px;
        color: var.$text-dark;
        font-weight: 600;
        background-color: var.$accent;
        border-radius: 4px;
        @include mixins.hover {
            background-color: var.$hover-primary;
        }
        &::after {
            margin-top: 1px;
            content: '';
            width: 16px;
            height: 16px;
            mask-image: url(../img/icons/arrow-thin.svg);
            mask-size: contain;
            background-color: var.$text-dark;
        }
    }
    &__items {
        margin-top: 32px;
        display: flex;
        flex-direction: column;
        gap: 24px;
        @include mixins.tablet {
            flex-wrap: wrap;
            flex-direction: row;
        }
        @include mixins.desktop {
            margin-top: 0;
        }
    }
    &__item {
        border-radius: 8px;
        border: 1px solid var.$accent;
        display: grid;
        grid-template-columns: 40px 1fr;
        gap: 8px 24px;
        padding: 23px;
        border-radius: 8px;
        @include mixins.tablet {
            width: calc(50% - 12px);
        }
        &:first-child, &:last-child {
            background-color: var.$accent;
            color: var.$text-dark;
            & .why__item-text {
                color: var.$text-dark;
            }
        }
        &:nth-child(3) {
            order: 4;
            @include mixins.tablet {
                order: 3;
            }
            & .why__item-text {
                max-width: 170px;
            }
        }
        &:last-child {
            order: 3;
            @include mixins.tablet {
                order: 4;
            }
            & .why__item-text {
                max-width: 170px;
            }
        }
    }
    &__item-icon {
        width: 40px;
        grid-row: 1/3;
    }
    &__item-title {
        font-weight: 700;
        font-size: 20px;
    }
    &__item-text {
        color: var.$text-primary;
    }
    

}