@use "../commons/var";
@use "../commons/mixins";

.error-page {
    padding: 80px 0;
    @include mixins.tablet {
        padding: 120px 0;
    }
    @include mixins.desktop {
        padding: 164px 0;
    }
    &__inner {
        @include mixins.tablet {
            @include mixins.flex-center-center;
            gap: 24px;
        }
    }
    &__number {
        display: none;
        @include mixins.tablet {
            display: block;
            flex: 0 0 176px;
        }
        @include mixins.desktop {
            flex: 0 0 384px;
        }
    }
    &__content {
        border: 1px solid var.$border;
        border-radius: 8px;
        background-color: var.$bg-secondary;
        padding: 23px;
        text-align: center;
        align-self: stretch;
        display: flex;
        flex-direction: column;
        justify-content: center;
    }
    &__info {
        display: inline-flex;
        align-items: center;
        gap: 4px;
        margin: 0 auto;
        font-weight: 700;
        font-size: 14px;
        border-radius: 4px;
        background-color: #FFFFFF1A;
        padding: 3px 11px 3px 3px;
        &::before {
            content: '';
            width: 20px;
            height: 20px;
            mask-image: url(../img/icons/info.svg);
            mask-position: center;
            mask-repeat: no-repeat;
            background-color: var.$text-primary;
        }
    }
    &__title {
        margin-top: 8px;
        font-weight: 800;
        font-size: 96px;
        padding-bottom: 8px;
        position: relative;
        &::after {
            position: absolute;
            content: '';
            width: 100%;
            height: 1px;
            background: linear-gradient(90deg, rgba(50, 50, 50, 0) 0%, #323232 50%, rgba(50, 50, 50, 0) 100%);
            left: 0;
            bottom: 0;
        }
    }
    &__text {
        padding-top: 24px;
        font-weight: 700;
        font-size: 20px;
        @include mixins.desktop {
            padding-top: 32px;
            font-size: 24px;
        }
    }
    &__description {
        margin-top: 16px;
        font-size: 14px;
        color: var.$text-primary;
        @include mixins.desktop {
            margin-top: 24px;
            font-size: 16px;
        }
    }
    &__link {
        display: block;
        margin-top: 24px;
        padding: 16px 24px;
        color: var.$text-dark;
        font-weight: 500;
        font-size: 14px;
        background-color: var.$accent;
        border-radius: 4px;
        @include mixins.hover {
            background-color: var.$hover-primary;
        }
        @include mixins.desktop {
            margin-top: auto;
        }
    }
}