@use "../../commons/var" as v;
@use "../../commons/mixins";

.advantages {
    border-top: 1px solid v.$border;
    padding: 48px 0 64px;
    border-radius: 32px 32px 0 0;
    background: linear-gradient(180deg, #1F1F1F 0%, rgba(31, 31, 31, 0) 100%);
    position: relative;
    z-index: 1;
    @include mixins.tablet {
        padding: 48px 0 96px;
    }
    &__description {
        margin-top: 28px;
        font-weight: 700;
        font-size: 14px;
        text-align: center;
        color: v.$text-primary;
        @include mixins.tablet {
            margin-top: 24px;
            font-size: 16px;    
        }
    }
    &__list {
        margin-top: 58px;
        border-radius: 24px;
        border: 1px solid v.$border;
        overflow: hidden;
        @include mixins.tablet {
            margin-top: 48px;
            display: grid;
            grid-template-columns: 1fr 1fr;
            border-radius: 32px;
        }
        @include mixins.desktop {
            grid-template-columns: 1fr 1fr 1fr;
            grid-template-rows: 1fr 1fr;
        }
    }
    &__item {
        padding: 39px 24px 40px;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        &:not(:first-child) {
            border-top: 1px solid v.$border;
        }
        @include mixins.hover {
            background-color: v.$white;
            & .advantages__name, .advantages__title, .advantages__subtitle, .advantages__text{
                color: v.$text-dark;
            }
        }
        @include mixins.tablet {
            padding: 32px;
            &:nth-child(even) {
                border-right: 1px solid v.$border;
            }
        }
        @include mixins.desktop {
            justify-content: center;
            padding: 40px 39px;
            &:nth-child(2) {
                border-top: 0;
                border-right: 0;
                padding-left: 39px;
            }
            &:nth-child(n + 3) {
                border-right: 1px solid v.$border;
            }
            &:last-child {
                border-right: 0;
            }
        }
        &:first-child {
            padding: 24px 24px 0;
            text-align: left;
            align-items: flex-start;
            & .advantages__img {
                align-self: center;
            }
            @include mixins.tablet {
                grid-column: 1/3;
                display: grid;
                grid-template-columns: 392px 1fr;
                padding: 32px 32px 0;
            }
            @include mixins.desktop {
                border-right: 1px solid v.$border;
                padding: 44px 56px 0 48px;
                gap: 0 32px;
                grid-template-columns: 400px 1fr;
            }
        }
    }
    &__title {
        font-weight: 700;
        font-size: 24px;
        font-family: v.$roboto;
        line-height: 1.2;
        transition: color .1s linear;
        @include mixins.tablet {
            font-size: 40px;
        }
        & span {
            color: v.$accent;
        }
    }
    &__subtitle {
        margin-top: 24px;
        color: v.$text-primary;
        transition: color .1s linear;
        @include mixins.tablet {
            margin-top: 50px;
            grid-row: 2/3;
            font-size: 20px;
            padding-bottom: 32px;
        }
        @include mixins.desktop {
            padding-bottom: 48px;
        }
    }
    &__img {
        margin-top: 32px;
        @include mixins.tablet {
            margin-top: 0;
            grid-row: 1/3;
            margin-left: auto;
            margin-top: auto;
        }
    }
    &__icon {
        width: 80px;
        height: 80px;
        & img {
            object-fit: contain;
            width: 100%;
        }
    }
    &__name {
        margin-top: 24px;
        font-size: 20px;
        font-weight: 700;
        transition: color .1s linear;
        @include mixins.tablet {
            font-size: 24px;
        }
    }
    &__text {
        margin: 8px auto 0;
        color: v.$text-primary;
        max-width: 325px;
        transition: color .1s linear;
        @include mixins.tablet {
            font-size: 20px;
        }
    }
}