@use "../../commons/var";
@use "../../commons/mixins";

.about-description {
    margin-top: 32px;
    @include mixins.tablet {
        margin-top: 64px;
    }
    @include mixins.desktop {
        margin: 64px 64px 0;
        @include mixins.flex-center-space;
        gap: 96px;
    }
    &__links {
        margin-top: 32px;
        display: flex;
        align-items: stretch;
        gap: 16px;
        @include mixins.desktop {
            margin-top: 0;
        }
    }
    &__bot {
        border: 1px solid var.$accent;
        background-color: var.$accent;
        @include mixins.flex-center;
        gap: 8px;
        font-weight: 500;
        color: var.$text-dark;
        border-radius: 4px;
        padding: 16px 42px;
        white-space: nowrap;
        transition: border .2s ease-in-out, background-color .2s ease-in-out;
        @include mixins.tablet {
            padding: 16px 23px 16px 17px;
        }
        &::before {
            content: '';
            width: 20px;
            height: 20px;
            background-color: var.$text-dark;
            mask: url(../img/icons/energy.svg) center no-repeat;
        }
        @media (hover: hover) {
            &:hover {
                border: 1px solid var.$hover-primary;
                background-color: var.$hover-primary;
            }
        }
    }
    &__docs {
        border: 1px solid var.$border;
        @include mixins.flex-center;
        gap: 8px;
        font-weight: 500;
        color: var.$text-light;
        border-radius: 4px;
        padding: 16px 23px 16px 17px;
        white-space: nowrap;
        transition: border .2s ease-in-out, background-color .2s ease-in-out;
        @include mixins.tablet {
            padding: 16px 23px 16px 17px;
        }
        &::before {
            content: '';
            width: 20px;
            height: 20px;
            background-color: var.$text-secondary;
            mask: url(../img/icons/history.svg) center no-repeat;
            transition: background-color .2s ease-in-out;
        }
        @media (hover: hover) {
            &:hover {
                border: 1px solid var.$text-light;
                &::before {
                    background-color: var.$text-light;
                }
            }
        }
    }
}   